import React, { useState, useEffect } from 'react';
import { motion, useAnimation, animate } from 'framer-motion';
import {
  Box, Container,
  Dialog, DialogContent, IconButton,
  Snackbar, SnackbarContent,
} from '@mui/material';
import {
  Close as CloseIcon,
  ArrowDropDown as ArrowDropDownIcon,
} from '@mui/icons-material';

import { rareDataList, sleep, convertColorcode } from '../context/Config';


type SetOpenSelectWildAnimationFunction = (animation: boolean) => void;
type SelectWildAnimationProps = {
  openSelectWildAnimation: boolean;
  setOpenSelectWildAnimation: SetOpenSelectWildAnimationFunction;
  lastSelectPoke: any;
  lastWildPoke: any;
}

const SelectWildAnimation: React.FC<SelectWildAnimationProps> = ({
  openSelectWildAnimation,
  setOpenSelectWildAnimation,
  lastSelectPoke,
  lastWildPoke,
}) => {
  const control01 = useAnimation();
  const control02 = useAnimation();
  const control03 = useAnimation();
  const control04 = useAnimation();

  const [controlNum, setControlNum] = useState<number>(0);
  const [message, setMessage] = useState<string>('');
  const [displayMessage, setDisplayMessage] = useState<string>('');


  // 1
  useEffect(() => {
    const controlText = animate(0, message.length, {
      duration: (message.length / 20),
      onUpdate: (latest: number) => {
        // 
        setDisplayMessage(message.slice(0, Math.round(latest)));
      },
    });
    return controlText.stop;
  }, [message]);

  // 
  useEffect(() => {
    if (controlNum === 0) {
      setControlNum(1);
    } else if (controlNum === 1) {
      setMessage(' ');
      control01.start({ scale: 0, opacity: 0 });
    } else if (controlNum === 2) {
      control02.start({
        rotate: [null, 24, 0, -21, 0, 18, 0, -15, 0, 12, 0, -9, 0, 6, 0, -3, 0],
        y: -1440
      });
    } else if (controlNum === 3) {
      control03.start({
        y: [-1200, -720, 0, -12, -18, -21, -18, -12, 0, -6, -9, -6, 0, -3, 0],
        rotate: [null, 3, 0, -6, 0, 9, 0, -12, 0, 15, 0, -18, 0, 21, 0, -24, 0]
      });
    } else if (controlNum === 4) {
      setMessage(' ');
      control04.start({ scale: 1, opacity: 1 });
    } else {
      setOpenSelectWildAnimation(false);
    }
    // eslint-disable-next-line
  }, [controlNum]);


  // 
  const handleAnimationComplete = async () => {
    await sleep(6000);
    setOpenSelectWildAnimation(false);
  };

  // (https://www.gradientmagic.com/browse)
  const background = `linear-gradient(to top, 
    rgba(${convertColorcode(rareDataList[lastWildPoke.rare].color[100])}, 0.8) 0%,
    rgba(${convertColorcode(rareDataList[lastWildPoke.rare].color[300])}, 0.8) 50%,
    rgba(${convertColorcode(rareDataList[lastWildPoke.rare].color[500])}, 0.8) 80%,
    rgba(${convertColorcode(rareDataList[lastWildPoke.rare].color[700])}, 0.8) 100%
    ), linear-gradient(90deg, rgba(125, 125, 125, 0.08) 0%, rgba(125, 125, 125, 0.08) 15%,rgba(79, 79, 79, 0.08) 15%, rgba(79, 79, 79, 0.08) 19%,rgba(146, 146, 146, 0.08) 19%, rgba(146, 146, 146, 0.08) 22%,rgba(178, 178, 178, 0.08) 22%, rgba(178, 178, 178, 0.08) 27%,rgba(185, 185, 185, 0.08) 27%, rgba(185, 185, 185, 0.08) 80%,rgba(92, 92, 92, 0.08) 80%, rgba(92, 92, 92, 0.08) 100%),linear-gradient(90deg, rgba(23, 23, 23, 0.07) 0%, rgba(23, 23, 23, 0.07) 10%,rgba(117, 117, 117, 0.07) 10%, rgba(117, 117, 117, 0.07) 12%,rgba(208, 208, 208, 0.07) 12%, rgba(208, 208, 208, 0.07) 18%,rgba(241, 241, 241, 0.07) 18%, rgba(241, 241, 241, 0.07) 57%,rgba(62, 62, 62, 0.07) 57%, rgba(62, 62, 62, 0.07) 65%,rgba(100, 100, 100, 0.07) 65%, rgba(100, 100, 100, 0.07) 72%,rgba(254, 254, 254, 0.07) 72%, rgba(254, 254, 254, 0.07) 100%),linear-gradient(90deg, rgba(231, 231, 231, 0.08) 0%, rgba(231, 231, 231, 0.08) 15%,rgba(130, 130, 130, 0.08) 15%, rgba(130, 130, 130, 0.08) 18%,rgba(251, 251, 251, 0.08) 18%, rgba(251, 251, 251, 0.08) 38%,rgba(107, 107, 107, 0.08) 38%, rgba(107, 107, 107, 0.08) 78%,rgba(151, 151, 151, 0.08) 78%, rgba(151, 151, 151, 0.08) 92%,rgba(121, 121, 121, 0.08) 92%, rgba(121, 121, 121, 0.08) 99%,rgba(91, 91, 91, 0.08) 99%, rgba(91, 91, 91, 0.08) 100%),linear-gradient(90deg, rgba(62, 62, 62, 0.06) 0%, rgba(62, 62, 62, 0.06) 18%,rgba(247, 247, 247, 0.06) 18%, rgba(247, 247, 247, 0.06) 28%,rgba(184, 184, 184, 0.06) 28%, rgba(184, 184, 184, 0.06) 46%,rgba(25, 25, 25, 0.06) 46%, rgba(25, 25, 25, 0.06) 100%),linear-gradient(90deg, rgba(99, 99, 99, 0.07) 0%, rgba(99, 99, 99, 0.07) 21%,rgba(115, 115, 115, 0.07) 21%, rgba(115, 115, 115, 0.07) 45%,rgba(234, 234, 234, 0.07) 45%, rgba(234, 234, 234, 0.07) 61%,rgba(44, 44, 44, 0.07) 61%, rgba(44, 44, 44, 0.07) 64%,rgba(185, 185, 185, 0.07) 64%, rgba(185, 185, 185, 0.07) 76%,rgba(66, 66, 66, 0.07) 76%, rgba(66, 66, 66, 0.07) 100%),linear-gradient(90deg, rgba(233, 233, 233, 0.01) 0%, rgba(233, 233, 233, 0.01) 5%,rgba(205, 205, 205, 0.01) 5%, rgba(205, 205, 205, 0.01) 71%,rgba(223, 223, 223, 0.01) 71%, rgba(223, 223, 223, 0.01) 85%,rgba(58, 58, 58, 0.01) 85%, rgba(58, 58, 58, 0.01) 91%,rgba(25, 25, 25, 0.01) 91%, rgba(25, 25, 25, 0.01) 98%,rgba(143, 143, 143, 0.01) 98%, rgba(143, 143, 143, 0.01) 100%),linear-gradient(90deg, rgba(181, 181, 181, 0.05) 0%, rgba(181, 181, 181, 0.05) 18%,rgba(115, 115, 115, 0.05) 18%, rgba(115, 115, 115, 0.05) 45%,rgba(80, 80, 80, 0.05) 45%, rgba(80, 80, 80, 0.05) 51%,rgba(236, 236, 236, 0.05) 51%, rgba(236, 236, 236, 0.05) 52%,rgba(165, 165, 165, 0.05) 52%, rgba(165, 165, 165, 0.05) 55%,rgba(238, 238, 238, 0.05) 55%, rgba(238, 238, 238, 0.05) 63%,rgba(50, 50, 50, 0.05) 63%, rgba(50, 50, 50, 0.05) 100%)`;
  // 
  const action = (
    <IconButton
      color='inherit'
      onClick={() => setControlNum(controlNum + 1)}
    >
      <ArrowDropDownIcon />
    </IconButton>
  );



  return (
    <Dialog
      open={openSelectWildAnimation}
      onClose={() => setOpenSelectWildAnimation(false)}
      fullScreen
      sx={{ zIndex: (theme) => theme.zIndex.drawer + 200 }}
    >
      {(lastSelectPoke.dexNum > 0 && lastWildPoke.dexNum > 0) &&
        <DialogContent
        sx={{
          background: background,
          overflow: 'hidden'
        }}
      >
        <Container maxWidth='xs' sx={{ p: 0 }}>
          <IconButton
            onClick={() => setOpenSelectWildAnimation(false)}
          >
            <CloseIcon sx={{ color: 'white' }} />
          </IconButton>
          <Box
            justifyContent='center'
            alignItems='center'
            sx={{
              mt: '50vh',
              textAlign: 'center',
            }}
          >
            {controlNum === 1 &&
              <motion.img
                initial={{ scale: 1, opacity: 1 }}
                animate={control01}
                transition={{ type: 'spring', duration: 1, delay: 3 }}
                src={lastSelectPoke.shiny
                  ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/shiny/${lastSelectPoke.dexNum}.gif`
                  : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/${lastSelectPoke.dexNum}.gif`
                }
                alt={`${lastSelectPoke.dexNum}`}
                onAnimationComplete={() => setControlNum(controlNum + 1)}
              >
              </motion.img>
            }

            {controlNum === 2 &&
              <motion.img
                initial={{ rotate: 0, y: 0 }}
                animate={control02}
                transition={{
                  rotate: { ease: 'easeOut', duration: 2, delay: 0 },
                  y: { ease: 'easeIn', duration: 1, delay: 3 },
                }}
                src={rareDataList[lastSelectPoke.rare].url}
                alt={`${lastSelectPoke.rare}`}
                onAnimationComplete={() => setControlNum(controlNum + 1)}
              >
              </motion.img>
            }

            {controlNum === 3 &&
              <motion.img
                initial={{ y: -1440, rotate: 0 }}
                animate={control03}
                transition={{
                  y: { ease: 'easeOut', duration: 2, delay: 0 },
                  rotate: { ease: 'easeIn', duration: 1, delay: 2 },
                }}
                src={rareDataList[lastWildPoke.rare].url}
                alt={`${lastWildPoke.rare}`}
                onAnimationComplete={() => setControlNum(controlNum + 1)}
              >
              </motion.img>
            }

            {controlNum === 4 &&
              <motion.img
                initial={{ scale: 0, opacity: 0 }}
                animate={control04}
                transition={{ type: 'spring', duration: 1, delay: 0 }}
                style={{ position: 'relative' }}
                src={lastWildPoke.shiny
                  ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/shiny/${lastWildPoke.dexNum}.gif`
                  : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/${lastWildPoke.dexNum}.gif`
                }
                alt={`${lastWildPoke.dexNum}`}
                onAnimationComplete={handleAnimationComplete}
              >
              </motion.img>
            }
          </Box>

          <Snackbar
            open={true}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'center',
            }}
          >
            <SnackbarContent
              message={displayMessage}
              action={action}
              sx={{ bgcolor: 'white', color: 'black' }}
            />
          </Snackbar>
          </Container>
        </DialogContent>
      }
    </Dialog>
  )
}

export default SelectWildAnimation;
