import React, { useState } from 'react';
import Chart from "react-apexcharts";
import {
  Box, Grid,
  Button, Paper,
  TableContainer, Table, TableHead, TableRow, TableCell, TableBody,
  CardMedia,
} from '@mui/material';
import {
  Update as UpdateIcon,
} from '@mui/icons-material';

import { rareDataList, truncateDecimal } from '../context/Config';
import { useStateContext } from '../context/StateContext';
import RequestUtil from '../context/RequestUtil';


type SimulationProps = {
  rare: number;
  exp: number;
  battleCount: number;
}

const BattleSimulation: React.FC<SimulationProps> = ({
  rare,
  exp,
  battleCount,
}) => {
  const {
    setMessage,
    setMessageOpen,
    setMessageSeverity,
    isRunning,
    setIsRunning,
    settingsList,
  } = useStateContext();
  const rares4DexNumList: number[] = settingsList.rares['4']['dexNumList'];
  const rares3DexNumList: number[] = settingsList.rares['3']['dexNumList'];
  const rares2DexNumList: number[] = settingsList.rares['2']['dexNumList'];
  const rares1DexNumList: number[] = settingsList.rares['1']['dexNumList'];
  const raresLength: number = Object.keys(settingsList.rares).length;
  const fetchPost = RequestUtil();

  // 
  const [expData, setExpData] = useState<number[]>([0]);
  const [battlePointData, setBattlePointData] = useState<number[]>([0]);
  const [countData, setCountData] = useState<number[]>([0]);
  const [winData, setWinData] = useState<boolean[]>([false]);
  const [dexNumData, setDexNumData] = useState<number[]>([]);


  // 
  const handleBattleSimulationClick = async () => {
    try {
      setIsRunning(true);

      const paramsList: Record<string, string> = {
        action: 'battleSimulation',
        rare: String(rare),
        exp: String(exp),
        battleCount: String(battleCount),
      };
      const returnValue = await fetchPost(paramsList);

      if (returnValue.result) {
        setExpData(returnValue.data.expData);
        setBattlePointData(returnValue.data.battlePointData);
        setCountData(returnValue.data.countData);
        setWinData(returnValue.data.winData);
        setDexNumData(returnValue.data.dexNumData);
      }

    } catch (error: unknown) {
      if (error instanceof Error) {
        setMessage(error.message);
        setMessageOpen(true);
        setMessageSeverity('error');
      } else {
        setMessage('');
        setMessageOpen(true);
        setMessageSeverity('error');
      }
    } finally {
      setIsRunning(false);
    }
  };


  const winCount = winData.filter((entry: any) => entry === true).length;

  const series = [
    {
      name: '',
      data: expData,
    },
    {
      name: 'BP',
      data: battlePointData,
    },
  ];

  const options = {
    chart: {
      id: 'line-chart',
      toolbar: {
        show: false,
      },
    },
    xaxis: {
      categories: countData,
      title: {
        text: '',
      },
      tickAmount: Math.min(countData.length, 10),
      decimalsInFloat: 0,
      forceNiceScale: true,
    },
    yaxis: [
      {
        title: {
          text: '',
        },
        tickAmount: 10,
        decimalsInFloat: 2,
        forceNiceScale: true,
        min: settingsList.minExp,
        max: settingsList.maxExp,
      },
      {
        opposite: true,
        title: {
          text: 'BP',
        },
        decimalsInFloat: 0,
        forceNiceScale: true,
        min: 0,
      },
    ],
    stroke: {
      width: 2,
    },
  };



  return (
    <Box sx={{ mt: 2 }}>
      <Grid container
        spacing={1}
        justifyContent='center'
        alignItems='center'
        sx={{ mt: 2, textAlign: 'center' }}
      >
        <Grid item xs={12} md={6}>
          <Button
            variant='contained'
            onClick={() => handleBattleSimulationClick()}
            disabled={
              isRunning ||
              rare < 0 ||
              rare > raresLength ||
              exp < settingsList.minExp ||
              exp > settingsList.maxExp ||
              battleCount < 1 ||
              battleCount > 999
            }
            startIcon={<UpdateIcon />}
            sx={{ width: '100%' }}
          >
            
          </Button>
        </Grid>
      </Grid>

      <Chart series={series} options={options} type='line' />

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell align='center'>
                
              </TableCell>
              <TableCell align='center'>
                
              </TableCell>
              <TableCell align='center'>
                
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody>
            <TableRow>
              <TableCell align='center'>
                {winCount}
              </TableCell>
              <TableCell align='center'>
                {countData.length - 1 - winCount}
              </TableCell>
              <TableCell align='center'>
                {`${truncateDecimal((winCount / (countData.length - 1)) * 100)}%`}
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>

      <Grid container
        spacing={0.4}
        sx={{ mt: 1 }}
        columns={10}
      >
        {dexNumData.map((dexNum, index) => {
          const rare = rares4DexNumList.includes(dexNum)
            ? 4 : rares3DexNumList.includes(dexNum)
              ? 3 : rares2DexNumList.includes(dexNum)
                ? 2 : rares1DexNumList.includes(dexNum)
                  ? 1 : 0;

          return (
            <Grid item
              key={index}
              xs={1}
            >
              {dexNum > 0 &&
                <Paper
                  elevation={winData[index] ? 5 : 0}
                  sx={{
                    bgcolor: rareDataList[rare].color[100],
                    border: winData[index] ? 'solid' : 'none',
                    borderColor: 'primary.main',
                  }}
                >
                  <CardMedia
                    component='img'
                    image={`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${dexNum}.png`}
                    alt={`${dexNum}`}
                  />
                </Paper>
              }
            </Grid>
          )
        })}
      </Grid>
    </Box>
  )
}

export default BattleSimulation;
