import React from 'react';
import Chart from "react-apexcharts";



type RadarChartProps = {
  data: number[];
}

const RadarChart: React.FC<RadarChartProps> = ({
  data
}) => {
  const series = [
    {
      data: data,
    },
  ];

  const options = {
    chart: {
      id: 'radar-chart',
      toolbar: {
        show: false,
      },
    },
    xaxis: {
      categories: ['HP', '', '', '', '', ''],
    },
    yaxis: {
      max: 255,
      min: 0,
      show: false,
    },
    fill: {
      opacity: 0.5,
    },
    markers: {
      size: 0,
    },
  };

  

  return (
    <div>
      <Chart series={series} options={options} type='radar' />
    </div>
  );
}

export default RadarChart;
