import React, { useState } from 'react';
import {
  Box, Grid,
  Tabs, Tab,
  Select, MenuItem, TextField,
} from '@mui/material';
import {
  Done as DoneIcon,
  DoneAll as DoneAllIcon,
} from '@mui/icons-material';

import { useStateContext } from '../context/StateContext';
import BattleSimulation from './BattleSimulation';
import BattleSimulationAll from './BattleSimulationAll';



const Simulation: React.FC = () => {
  const {
    settingsList,
  } = useStateContext();
  const raresLength: number = Object.keys(settingsList.rares).length;

  const [tabValue, setTabValue] = React.useState('simulation');
  // 
  const [rare, setRare] = useState<number>(0);
  // 
  const [exp, setExp] = useState<number>(1);
  // 
  const [battleCount, setBattleCount] = useState<number>(100);



  return (
    <Box sx={{ mt: 2 }}>
      <Tabs
        value={tabValue}
        onChange={(event, newTabValue) => setTabValue(newTabValue)}
        indicatorColor='primary'
        textColor='primary'
        variant='fullWidth'
      >
        <Tab icon={<DoneIcon />} value='simulation' />
        <Tab icon={<DoneAllIcon />} value='simulationAll' />
      </Tabs>

      <Grid container
        spacing={1}
        justifyContent='center'
        alignItems='center'
        sx={{ mt: 2, textAlign: 'center' }}
      >
        <Grid item xs={4}>
          {tabValue === 'simulation' &&
            <Select
              value={rare}
              onChange={(event) => setRare(event.target.value as number)}
            >
              {Array.from({ length: raresLength + 1 }, (_, index) => (
                <MenuItem key={index}
                  value={index}
                >
                  {index}
                </MenuItem>
              ))}
            </Select>
          }
        </Grid>

        <Grid item xs={4}>
          <TextField type='number'
            label=''
            value={exp}
            onChange={(event) => setExp(Number(event.target.value))}
            error={exp < settingsList.minExp || exp > settingsList.maxExp}
            inputProps={{
              min: settingsList.minExp,
              max: settingsList.maxExp,
            }}
          />
        </Grid>

        <Grid item xs={4}>
          <TextField type='number'
            label=''
            value={battleCount}
            onChange={(event) => setBattleCount(Number(event.target.value))}
            error={battleCount < 1 || battleCount > 999}
            inputProps={{
              min: 1,
              max: 999,
            }}
          />
        </Grid>
      </Grid>

      {tabValue === 'simulation' &&
        <BattleSimulation
          rare={rare}
          exp={exp}
          battleCount={battleCount}
        />
      }

      {tabValue === 'simulationAll' &&
        <BattleSimulationAll
          exp={exp}
          battleCount={battleCount}
        />
      }
    </Box>
  )
}

export default Simulation;
