import React from 'react';
import {
  Box, Grid,
  Button,
} from '@mui/material';

import { useStateContext } from '../context/StateContext';
import { gachaTypeList } from '../context/Config';
import RequestUtil from '../context/RequestUtil';



type SetDisplayFunction = (display: string) => void;
type SetAddGachaCountFunction = (addGachaCount: number) => void;
type SetAddPokeExpFunction = (addPokeExp: number) => void;
type SetOpenGachaAnimationFunction = (openGachaAnimation: boolean) => void;
type SetGachaTypeFunction = (gachaType: string) => void;
type GachaProps = {
  setDisplay: SetDisplayFunction;
  setAddGachaCount: SetAddGachaCountFunction;
  setAddPokeExp: SetAddPokeExpFunction;
  setOpenGachaAnimation: SetOpenGachaAnimationFunction;
  setGachaType: SetGachaTypeFunction;
}

const Gacha: React.FC<GachaProps> = ({
  setDisplay,
  setAddGachaCount,
  setAddPokeExp,
  setOpenGachaAnimation,
  setGachaType,
}) => {
  const {
    setMessage,
    setMessageOpen,
    setMessageSeverity,
    isRunning,
    setIsRunning,
    userInfo,
    setUserInfo,
    settingsList,
  } = useStateContext();
  const fetchPost = RequestUtil();


  // 
  const handleGachaClick = async (gachaType: string) => {
    try {
      setIsRunning(true);
      setAddGachaCount(0);
      setAddPokeExp(0);
      setGachaType(gachaType);

      const paramsList: Record<string, string> = {
        action: 'gacha',
        gachaType: gachaType,
      };
      const returnValue = await fetchPost(paramsList);

      if (returnValue.result) {
        setOpenGachaAnimation(true);
        setUserInfo(returnValue.data.userInfo);
        setDisplay('selectWild');
      }

    } catch (error: unknown) {
      if (error instanceof Error) {
        setMessage(error.message);
        setMessageOpen(true);
        setMessageSeverity('error');
      } else {
        setMessage('');
        setMessageOpen(true);
        setMessageSeverity('error');
      }
    } finally {
      setIsRunning(false);
    }
  };


  // 
  const gachaList = gachaTypeList.map((gacha, index) => ({
    id: gacha.id,
    name: `${gacha.name}${index === 0
      ? settingsList.normalGachaCost
      : (index === 1
        ? settingsList.premierGachaCost
        : settingsList.luxuryGachaCost)}
        `,
    disabled: userInfo.gachaCount < (index === 0
      ? settingsList.normalGachaCost
      : (index === 1
        ? settingsList.premierGachaCost
        : settingsList.luxuryGachaCost)),
    url: gacha.url,
  }));



  return (
    <Box>
      <Grid container
        spacing={2}
        justifyContent='space-around'
        alignItems='center'
        sx={{ mt: 2, textAlign: 'center' }}
      >
        {gachaList.map((gacha) => (
          <Grid item
            xs={12} md={4}
            key={gacha.id}
          >
            <Button
              variant='contained'
              onClick={() => handleGachaClick(gacha.id)}
              disabled={isRunning || gacha.disabled}
              sx={{ width: '100%' }}
              startIcon={
                <img
                  src={gacha.url}
                  alt={gacha.id}
                />
              }>
              {gacha.name}
            </Button>
          </Grid>
        ))}
      </Grid>
    </Box>
  )
}

export default Gacha;
