import React, { useState, useEffect } from 'react';
import {
  Box, Grid,
  TableContainer, Table, TableHead, TableRow, TableCell, TableBody, Paper, Typography,
  Card, CardActionArea, CardMedia,
  Dialog, DialogTitle, DialogContent, DialogActions, IconButton,
} from '@mui/material';
import {
  FormatListNumbered as FormatListNumberedIcon,
  Person as PersonIcon,
  Scoreboard as ScoreboardIcon,
  Repeat as RepeatIcon,
  CalendarMonth as CalendarMonthIcon,
  Attractions as AttractionsIcon,
  SportsMma as SportsMmaIcon,
  CatchingPokemon as CatchingPokemonIcon,
  AutoAwesome as AutoAwesomeIcon,
  Close as CloseIcon,
} from '@mui/icons-material';
import {
  yellow, blueGrey, brown, grey,
} from '@mui/material/colors';

import { useStateContext } from '../context/StateContext';
import { rareDataList, truncateDecimal } from '../context/Config';
import RequestUtil from '../context/RequestUtil';



type HandleCardClickFunction = (dexNum: number, rare: number) => void;
type RankingProps = {
  handleCardClick: HandleCardClickFunction;
}

const Ranking: React.FC<RankingProps> = ({
  handleCardClick,
}) => {
  const {
    setMessage,
    setMessageOpen,
    setMessageSeverity,
    setIsRunning,
    userInfo,
  } = useStateContext();
  const fetchPost = RequestUtil();

  const [ranking, setRanking] = useState<any>([]);
  const [openDialog, setOpenDialog] = useState<boolean>(false);
  const [rankIndex, setRankIndex] = useState<number>(0);


  // 
  useEffect(() => {
    (async () => {
      try {
        setIsRunning(true);

        const paramsList: Record<string, string> = {
          action: 'ranking',
        };
        const returnValue = await fetchPost(paramsList);

        if (returnValue.result) {
          setRanking(returnValue.data.ranking);
        }

      } catch (error: unknown) {
        if (error instanceof Error) {
          setMessage(error.message);
          setMessageOpen(true);
          setMessageSeverity('error');
        } else {
          setMessage('');
          setMessageOpen(true);
          setMessageSeverity('error');
        }
      } finally {
        setIsRunning(false);
      }
    })()
    // eslint-disable-next-line
  }, []);


  // 
  const handleTableRowClick = (index: number) => {
    setOpenDialog(true);
    setRankIndex(index);
  };



  return (
    <Box sx={{ mt: 1 }}>
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>
                <FormatListNumberedIcon />
              </TableCell>

              <TableCell align='center'>
                <PersonIcon />
                <ScoreboardIcon />
              </TableCell>

              <TableCell align='center'>
                <CatchingPokemonIcon />
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody>
            {ranking.map((rank: any, index: number) => (
              <TableRow
                key={index + 1}
                sx={{
                  bgcolor: index === 0 ? yellow[100]
                    : (index === 1 ? blueGrey[100]
                      : (index === 2 ? brown[100]
                        : grey[100]))
                }}>
                <TableCell align='center'
                  onClick={() => handleTableRowClick(index)}
                  sx={{
                    fontWeight: userInfo.userName === rank.userName ? 'bold' : 'normal',
                    cursor: 'pointer'
                  }}
                >
                  {index + 1}
                </TableCell>

                <TableCell align='center'
                  onClick={() => handleTableRowClick(index)}
                  sx={{
                    fontWeight: userInfo.userName === rank.userName ? 'bold' : 'normal',
                    cursor: 'pointer'
                  }}
                >
                  <Grid container direction='column' spacing={1}>
                    <Grid item>
                      {rank.userName}
                    </Grid>
                    <Grid item>
                      {Math.floor(rank.score)}
                    </Grid>
                  </Grid>
                </TableCell>

                <TableCell align='center'>
                  <Grid container spacing={1}>
                    {rank.pokeList.map((poke: any, i: number) => (
                      <Grid item
                        xs={4} md={2}
                        key={`${index}-${i}`}
                      >
                        <Card
                          sx={{
                            bgcolor: rareDataList[poke.rare].color[100]
                          }}
                        >
                          <CardActionArea
                            onClick={() => handleCardClick(poke.dexNum, poke.rare)}
                            disabled={poke.dexNum <= 0}
                          >
                            {poke.dexNum > 0 &&
                              <CardMedia
                                component='img'
                                image={poke.shiny
                                  ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/shiny/${poke.dexNum}.png`
                                  : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${poke.dexNum}.png`
                                }
                                alt={`${poke.dexNum}`}
                              />
                            }
                          </CardActionArea>
                        </Card>
                      </Grid>
                    ))}
                  </Grid>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      {openDialog &&
        <Dialog
          onClose={() => setOpenDialog(false)}
          open={openDialog}
          sx={{ textAlign: 'center' }}
        >
          <DialogTitle>
            {ranking[rankIndex].userName}
          </DialogTitle>

          <DialogContent>
            <TableContainer component={Paper}>
              <Table>
                <TableBody>
                  <TableRow>
                    <TableCell align='center'>
                      <ScoreboardIcon />
                      <Typography>
                        
                      </Typography>
                    </TableCell>
                    <TableCell align='center'>
                      {truncateDecimal(ranking[rankIndex].score)}
                    </TableCell>
                  </TableRow>

                  <TableRow>
                    <TableCell align='center'>
                      <RepeatIcon />
                      <Typography>
                        
                      </Typography>
                    </TableCell>
                    <TableCell align='center'>
                      {ranking[rankIndex].runningDays}
                    </TableCell>
                  </TableRow>

                  <TableRow>
                    <TableCell align='center'>
                      <SportsMmaIcon />
                      <Typography>
                        
                      </Typography>
                    </TableCell>
                    <TableCell align='center'>
                      {ranking[rankIndex].battleTotal}
                    </TableCell>
                  </TableRow>

                  <TableRow>
                    <TableCell align='center'>
                      <AttractionsIcon />
                      <Typography>
                        
                      </Typography>
                    </TableCell>
                    <TableCell align='center'>
                      {ranking[rankIndex].gachaTotal}
                    </TableCell>
                  </TableRow>

                  <TableRow>
                    <TableCell align='center'>
                      <CalendarMonthIcon />
                      <Typography>
                        
                      </Typography>
                    </TableCell>
                    <TableCell align='center'>
                      {ranking[rankIndex].lastDate}
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>

            <Grid container
              spacing={1}
              sx={{ mt: 2 }}
            >
              {ranking[rankIndex].pokeList.map((poke: any, i: number) => (
                <Grid item
                  xs={4} md={2}
                  key={`${i}`}
                >
                  <Card
                    sx={{
                      bgcolor: rareDataList[poke.rare].color[100]
                    }}
                  >
                    <CardActionArea
                      onClick={() => handleCardClick(poke.dexNum, poke.rare)}
                      disabled={poke.dexNum <= 0}
                    >
                      {poke.dexNum > 0 &&
                        <CardMedia
                          component='img'
                          image={poke.shiny
                            ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/shiny/${poke.dexNum}.png`
                            : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${poke.dexNum}.png`
                          }
                          alt={`${poke.dexNum}`}
                        />
                      }

                      <Grid container
                        alignItems='center'
                        sx={{ mt: 1 }}
                      >
                        <Grid item xs={2}>
                          {poke.shiny &&
                            <AutoAwesomeIcon sx={{ fontSize: 'inherit', ml: 0.75 }} />
                          }
                        </Grid>
                        <Grid item xs={8}>
                          <Typography
                            variant='subtitle2'
                            sx={{
                              display: 'flex',
                              alignItems: 'center',
                              justifyContent: 'center'
                            }}
                          >
                            {truncateDecimal(poke.exp)}

                          </Typography>
                        </Grid>
                      </Grid>
                    </CardActionArea>
                  </Card>
                </Grid>
              ))}
            </Grid>
          </DialogContent>

          <DialogActions sx={{ justifyContent: 'center' }}>
            <IconButton
              onClick={() => setOpenDialog(false)}
              color='inherit'
            >
              <CloseIcon />
            </IconButton>
          </DialogActions>
        </Dialog>
      }
    </Box>
  )
}

export default Ranking;
