import React, { useState, useEffect } from 'react';
import { motion, useAnimation, animate } from 'framer-motion';
import {
  Box, Container, Grid,
  Paper,
  Dialog, DialogContent, IconButton,
  Snackbar, SnackbarContent,
} from '@mui/material';
import {
  Close as CloseIcon,
  ArrowDropDown as ArrowDropDownIcon,
  AutoAwesome as AutoAwesomeIcon,
  NavigateBefore as NavigateBeforeIcon,
  NavigateNext as NavigateNextIcon,
} from '@mui/icons-material';
import {
  cyan,
  green,
  lightGreen,
} from '@mui/material/colors';

import { useStateContext } from '../context/StateContext';
import { rareDataList, sleep, truncateDecimal, convertColorcode } from '../context/Config';


type SetOpenBattleAnimationFunction = (animation: boolean) => void;
type BattleAnimationProps = {
  selectNum: number;
  addPokeExp: number;
  openBattleAnimation: boolean;
  setOpenBattleAnimation: SetOpenBattleAnimationFunction;
  lastSelectPoke: any;
  win: boolean;
}

const BattleAnimation: React.FC<BattleAnimationProps> = ({
  selectNum,
  addPokeExp,
  openBattleAnimation,
  setOpenBattleAnimation,
  lastSelectPoke,
  win,
}) => {
  const {
    userInfo,
    settingsList,
  } = useStateContext();
  const control01 = useAnimation();
  const control02 = useAnimation();
  const control03 = useAnimation();
  const control04 = useAnimation();
  const control05 = useAnimation();
  const control06 = useAnimation();

  const [controlNum, setControlNum] = useState<number>(0);
  const [message, setMessage] = useState<string>('');
  const [displayMessage, setDisplayMessage] = useState<string>('');
  const [displayExp, setDisplayExp] = useState<number>(truncateDecimal(lastSelectPoke.exp));


  // 1
  useEffect(() => {
    const controlText = animate(0, message.length, {
      duration: (message.length / 20),
      onUpdate: (latest: number) => {
        // 
        setDisplayMessage(message.slice(0, Math.round(latest)));
      },
    });
    return controlText.stop;
  }, [message]);

  // 
  useEffect(() => {
    if (controlNum === 0) {
      setControlNum(1);
    } else if (controlNum === 1) {
      setMessage(`   ${userInfo.wild_rare > 0 ? `` : ``}`);
      control01.start({ x: 0 });
      control02.start({ x: 0 });
    } else if (controlNum === 2) {
      setMessage(`${lastSelectPoke.rare > 0 ? `` : ``}`);
      control03.start({ x: 0 });
      control04.start({ x: 0 });
    } else if (controlNum === 3) {
      const wild = document.getElementById('wild');
      const lastSelect = document.getElementById('lastSelect');
      if (wild && lastSelect) {
        const wildRect = wild.getBoundingClientRect();
        const lastSelectRect = lastSelect.getBoundingClientRect();

        const duration = 0.8;
        let newMessage = '';
        let wildDelay = 0;
        let lastSelectDelay = 0;
        let wildRepeat = 0;
        let lastSelectRepeat = 0;

        if (lastSelectPoke.rare > userInfo.wild_rare) {
          newMessage = ' ';
          wildDelay = duration;
          wildRepeat = win ? 0 : 0;
          lastSelectRepeat = win ? 1 : 0;
        } else if (lastSelectPoke.rare < userInfo.wild_rare) {
          newMessage = '';
          lastSelectDelay = duration;
          wildRepeat = win ? 1 : 2;
          lastSelectRepeat = win ? 1 : 1;
        } else {
          newMessage = ' ';
          lastSelectDelay = duration;
          wildRepeat = win ? 2 : 3;
          lastSelectRepeat = win ? 2 : 2;
        }
        const scaleDelay = duration * (wildRepeat + lastSelectRepeat + 2);
        const wildScale = win ? [1, 0] : 1;
        const lastSelectScale = win ? 1 : [1, 0];

        setMessage(newMessage);
        control05.start({
          x: [0, -30, lastSelectRect.left - wildRect.left, -30, 0],
          y: [0, 30, lastSelectRect.bottom - wildRect.bottom, 30, 0],
          rotate: [0, 0, 0, 0, 27, 0, -36, 0, 27, 0, -18, 0, 0, 0, 0, 0],
          scale: wildScale,
          transition: {
            duration: duration,
            delay: wildDelay,
            repeat: wildRepeat,
            repeatDelay: duration,
            scale: {
              duration: 0.5,
              delay: scaleDelay,
            },
          }
        });
        control06.start({
          x: [0, 30, wildRect.right - lastSelectRect.right, 30, 0],
          y: [0, -30, wildRect.top - lastSelectRect.top, -30, 0],
          rotate: [0, 0, 0, 0, -27, 0, 36, 0, -27, 0, 18, 0, 0, 0, 0, 0],
          scale: lastSelectScale,
          transition: {
            duration: duration,
            delay: lastSelectDelay,
            repeat: lastSelectRepeat,
            repeatDelay: duration,
            scale: {
              duration: 0.5,
              delay: scaleDelay,
            },
          }
        });
      }
    } else if (controlNum === 4) {
      setMessage(`  ${win ? `` : ``}`);
      handleAddPokeExp();
      handleAnimationComplete();
    } else {
      setOpenBattleAnimation(false);
    }
    // eslint-disable-next-line
  }, [controlNum]);


  // 
  const handleAddPokeExp = () => {
    const controlAddPokeExp = animate(lastSelectPoke.exp, userInfo.pokeList[selectNum - 1].exp, {
      duration: 1,
      onUpdate: (latest: number) => {
        setDisplayExp(truncateDecimal(latest));
      },
    });
    return controlAddPokeExp.stop;
  };

  // 
  const handleAnimationComplete = async () => {
    await sleep(6000);
    setOpenBattleAnimation(false);
  };

  // (https://www.gradientmagic.com/browse)
  const background = `linear-gradient(180deg, rgba(6, 6, 6, 0.01) 0%, rgba(6, 6, 6, 0.01) 1%,rgba(131, 131, 131, 0.01) 1%, rgba(131, 131, 131, 0.01) 14%,rgba(250, 250, 250, 0.01) 14%, rgba(250, 250, 250, 0.01) 26%,rgba(30, 30, 30, 0.01) 26%, rgba(30, 30, 30, 0.01) 62%,rgba(117, 117, 117, 0.01) 62%, rgba(117, 117, 117, 0.01) 66%,rgba(248, 248, 248, 0.01) 66%, rgba(248, 248, 248, 0.01) 76%,rgba(39, 39, 39, 0.01) 76%, rgba(39, 39, 39, 0.01) 100%),linear-gradient(180deg, rgba(57, 57, 57, 0.06) 0%, rgba(57, 57, 57, 0.06) 4%,rgba(227, 227, 227, 0.06) 4%, rgba(227, 227, 227, 0.06) 26%,rgba(67, 67, 67, 0.06) 26%, rgba(67, 67, 67, 0.06) 27%,rgba(126, 126, 126, 0.06) 27%, rgba(126, 126, 126, 0.06) 39%,rgba(103, 103, 103, 0.06) 39%, rgba(103, 103, 103, 0.06) 72%,rgba(16, 16, 16, 0.06) 72%, rgba(16, 16, 16, 0.06) 76%,rgba(21, 21, 21, 0.06) 76%, rgba(21, 21, 21, 0.06) 88%,rgba(69, 69, 69, 0.06) 88%, rgba(69, 69, 69, 0.06) 100%),linear-gradient(180deg, rgba(156, 156, 156, 0.05) 0%, rgba(156, 156, 156, 0.05) 12%,rgba(54, 54, 54, 0.05) 12%, rgba(54, 54, 54, 0.05) 48%,rgba(169, 169, 169, 0.05) 48%, rgba(169, 169, 169, 0.05) 59%,rgba(104, 104, 104, 0.05) 59%, rgba(104, 104, 104, 0.05) 62%,rgba(165, 165, 165, 0.05) 62%, rgba(165, 165, 165, 0.05) 68%,rgba(124, 124, 124, 0.05) 68%, rgba(124, 124, 124, 0.05) 77%,rgba(189, 189, 189, 0.05) 77%, rgba(189, 189, 189, 0.05) 85%,rgba(173, 173, 173, 0.05) 85%, rgba(173, 173, 173, 0.05) 100%),linear-gradient(180deg, rgba(182, 182, 182, 0.07) 0%, rgba(182, 182, 182, 0.07) 22%,rgba(122, 122, 122, 0.07) 22%, rgba(122, 122, 122, 0.07) 28%,rgba(62, 62, 62, 0.07) 28%, rgba(62, 62, 62, 0.07) 44%,rgba(89, 89, 89, 0.07) 44%, rgba(89, 89, 89, 0.07) 61%,rgba(110, 110, 110, 0.07) 61%, rgba(110, 110, 110, 0.07) 83%,rgba(185, 185, 185, 0.07) 83%, rgba(185, 185, 185, 0.07) 86%,rgba(192, 192, 192, 0.07) 86%, rgba(192, 192, 192, 0.07) 100%),linear-gradient(180deg, rgba(8, 8, 8, 0.06) 0%, rgba(8, 8, 8, 0.06) 54%,rgba(48, 48, 48, 0.06) 54%, rgba(48, 48, 48, 0.06) 57%,rgba(245, 245, 245, 0.06) 57%, rgba(245, 245, 245, 0.06) 86%,rgba(12, 12, 12, 0.06) 86%, rgba(12, 12, 12, 0.06) 94%,rgba(225, 225, 225, 0.06) 94%, rgba(225, 225, 225, 0.06) 100%)
    , linear-gradient(6deg,
    rgba(${convertColorcode(green[300])}, 0.8) 0%,
    rgba(${convertColorcode(lightGreen[200])}, 0.8) 40%,
    rgba(${convertColorcode(green[200])}, 0.8) 70%,
    rgba(${convertColorcode(cyan[300])}, 0.8) 90%,
    rgba(${convertColorcode(cyan[400])}, 0.8) 100%
    )`;
  // 
  const action = (
    <IconButton
      color='inherit'
      onClick={() => setControlNum(controlNum + 1)}
    >
      <ArrowDropDownIcon />
    </IconButton>
  );



  return (
    <Dialog
      open={openBattleAnimation}
      onClose={() => setOpenBattleAnimation(false)}
      fullScreen
      sx={{ zIndex: (theme) => theme.zIndex.drawer + 200 }}
    >
      {(lastSelectPoke.dexNum > 0 && userInfo.wild_dexNum > 0) &&
        <DialogContent
          sx={{
            background: background,
            overflow: 'hidden'
          }}
        >
          <Container maxWidth='xs' sx={{ p: 0 }}>
            <IconButton
              onClick={() => setOpenBattleAnimation(false)}
            >
              <CloseIcon sx={{ color: 'white' }} />
            </IconButton>
            <Grid container
              justifyContent='center'
              alignItems='center'
              sx={{
                mt: '20vh',
                textAlign: 'center',
                width: '100%',
              }}
            >
              <Grid item xs={6}>
                <motion.div
                  initial={{ x: -1920 }}
                  animate={control02}
                  transition={{
                    x: {
                      type: 'spring',
                      bounce: 0.1,
                      mass: 0.4,
                      ease: 'easeIn',
                      duration: 1,
                      delay: 0.8
                    }
                  }}
                  onAnimationComplete={() => setControlNum(controlNum + 1)}
                >
                  <Paper
                    sx={{
                      mr: '20%',
                      width: '80%',
                      color: 'white',
                      background: rareDataList[userInfo.wild_rare].color[700],
                    }}
                  >
                    <Grid container
                      justifyContent='center'
                      alignItems='center'
                      sx={{
                        textAlign: 'center',
                      }}
                    >
                      <Grid item xs={2}>
                        {userInfo.wild_shiny &&
                          <AutoAwesomeIcon sx={{ fontSize: 'inherit' }} />
                        }
                      </Grid>
                      <Grid item xs={8}>
                        {settingsList.initialPokeExp}
                      </Grid>
                      <Grid item xs={2}>
                        <NavigateNextIcon sx={{ fontSize: 'inherit' }} />
                      </Grid>
                    </Grid>
                  </Paper>
                </motion.div>
              </Grid>

              <Grid item xs={6}>
                <motion.div
                  initial={{ rotate: 0, scale: 1 }}
                  animate={control05}
                  id='wild'
                >
                  <motion.img
                    initial={{ x: 1920 }}
                    animate={control01}
                    transition={{
                      x: { duration: 1, delay: 0 }
                    }}
                    src={userInfo.wild_shiny
                      ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/shiny/${userInfo.wild_dexNum}.gif`
                      : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/${userInfo.wild_dexNum}.gif`
                    }
                    alt={`${userInfo.wild_dexNum}`}
                  >
                  </motion.img>
                </motion.div>
              </Grid>
            </Grid>

            <Grid container
              justifyContent='center'
              alignItems='center'
              sx={{
                mt: '20vh',
                textAlign: 'center',
                width: '100%',
              }}
            >
              <Grid item xs={6}>
                <motion.div
                  initial={{ rotate: 0, scale: 1 }}
                  animate={control06}
                  onAnimationComplete={() => setControlNum(controlNum + 1)}
                  id='lastSelect'
                >
                  <motion.img
                    initial={{ x: -1920 }}
                    animate={control03}
                    transition={{
                      x: { duration: 1, delay: 0 }
                    }}
                    src={lastSelectPoke.shiny
                      ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/back/shiny/${lastSelectPoke.dexNum}.gif`
                      : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/back/${lastSelectPoke.dexNum}.gif`
                    }
                    alt={`${lastSelectPoke.dexNum}`}
                  >
                  </motion.img>
                </motion.div>
              </Grid>

              <Grid item xs={6}>
                <motion.div
                  initial={{ x: 1920 }}
                  animate={control04}
                  transition={{
                    x: {
                      type: 'spring',
                      bounce: 0.1,
                      mass: 0.4,
                      ease: 'easeIn',
                      duration: 1,
                      delay: 0.8
                    }
                  }}
                  onAnimationComplete={() => setControlNum(controlNum + 1)}
                >
                  <Paper
                    sx={{
                      ml: '20%',
                      width: '80%',
                      color: 'white',
                      background: rareDataList[lastSelectPoke.rare].color[700]
                    }}
                  >
                    <Grid container
                      justifyContent='center'
                      alignItems='center'
                      sx={{
                        textAlign: 'center',
                      }}
                    >
                      <Grid item xs={2}>
                        <NavigateBeforeIcon sx={{ fontSize: 'inherit' }} />
                      </Grid>
                      <Grid item xs={8}>
                        {displayExp}
                      </Grid>
                      <Grid item xs={2}>
                        {lastSelectPoke.shiny &&
                          <AutoAwesomeIcon sx={{ fontSize: 'inherit' }} />
                        }
                      </Grid>
                    </Grid>
                  </Paper>
                </motion.div>
              </Grid>
            </Grid>

            <Snackbar
              open={true}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'center',
              }}
            >
              <SnackbarContent
                message={displayMessage}
                action={action}
                sx={{ bgcolor: 'white', color: 'black' }}
              />
            </Snackbar>
          </Container>
        </DialogContent>
      }
    </Dialog>
  )
}

export default BattleAnimation;
