import React, { useState } from 'react';
import {
  Box, Grid,
  Typography,
  Select, MenuItem,
  Switch, FormControlLabel,
  Stack, Slider,
  Card, CardActionArea, CardMedia,
} from '@mui/material';
import {
  Grid3x3 as Grid3x3Icon,
  Grid4x4 as Grid4x4Icon,
} from '@mui/icons-material';

import { useStateContext } from '../context/StateContext';
import { rareDataList } from '../context/Config';


type HandleCardClickFunction = (dexNum: number, rare: number) => void;
type DisplayDexListProps = {
  handleCardClick: HandleCardClickFunction;
}

const DisplayDexList: React.FC<DisplayDexListProps> = ({
  handleCardClick,
}) => {
  const {
    settingsList,
  } = useStateContext();
  const dexMax: number = settingsList.dexMax;
  const dexRange: number = settingsList.dexRange;
  const rares4DexNumList: number[] = settingsList.rares['4']['dexNumList'];
  const rares3DexNumList: number[] = settingsList.rares['3']['dexNumList'];
  const rares2DexNumList: number[] = settingsList.rares['2']['dexNumList'];
  const rares1DexNumList: number[] = settingsList.rares['1']['dexNumList'];
  const raresLength: number = Object.keys(settingsList.rares).length;

  // 
  const [isDexRange, setIsDexRange] = useState<boolean>(true);
  // -1
  const [isRares, setIsRares] = useState<number>(-1);
  // 
  const [listRowNum, setListRowNum] = useState<number>(10);

  // 
  const renderDexList = () => {
    // 
    const dexList = [];
    // 
    let dexListCount = 0;
    // 
    for (let i = 1; i <= (isDexRange ? dexRange : dexMax); i++) {
      // URL
      const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${i}.png`; //1017
      //const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/shiny/${i}.png` //905
      //const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/back/${i}.png` //649
      //const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/back/shiny/${i}.png` //649

      //const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/${i}.gif` //649
      //const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/shiny/${i}.gif` //649
      //const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/back/${i}.gif` //649
      //const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/back/shiny/${i}.gif` //649

      //const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/official-artwork/${i}.png` //1017 exc:1013
      //const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/official-artwork/shiny/${i}.png` //1008

      //const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/dream-world/${i}.svg` //649
      //const imageUrl = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/home/${i}.png` //905

      // 
      const rare = rares4DexNumList.includes(i)
        ? 4 : rares3DexNumList.includes(i)
          ? 3 : rares2DexNumList.includes(i)
            ? 2 : rares1DexNumList.includes(i)
              ? 1 : 0;

      // 
      const display = (isRares !== 4 || rare === 4)
        && (isRares !== 3 || rare === 3)
        && (isRares !== 2 || rare === 2)
        && (isRares !== 1 || rare === 1)
        && (isRares !== 0 || rare === 0);

      // 
      if (display) {
        dexListCount += 1;
      }

      dexList.push(
        <Grid item
          key={i}
          xs={1}
          sx={{
            display: display ? 'block' : 'none'
          }}
        >
          <Card
            sx={{ bgcolor: rareDataList[rare].color[100] }}
          >
            <CardActionArea
              onClick={() => handleCardClick(i, rare)}
            >
              <CardMedia
                component='img'
                image={imageUrl}
                alt={`${i}`}
              />
            </CardActionArea>
          </Card>
        </Grid>
      );
    }
    return [dexList, dexListCount];
  };
  const [dexList, dexListCount] = renderDexList();

  const marks = [
    {
      value: 4,
      label: '4',
    },
    {
      value: 8,
      label: '8',
    },
    {
      value: 10,
      label: '10',
    },
    {
      value: 15,
      label: '15',
    },
    {
      value: 20,
      label: '20',
    },
  ];



  return (
    <Box sx={{ mt: 2 }}>
      <Grid container
        spacing={1}
        justifyContent='center'
        alignItems='center'
        sx={{ mt: 2, textAlign: 'center' }}
      >
        <Grid item xs={3}>
          <Typography>{dexListCount}</Typography>
        </Grid>

        <Grid item xs={4}>
          <Select
            value={isRares}
            onChange={(event) => setIsRares(event.target.value as number)}
          >
            <MenuItem value={-1}></MenuItem>
            {Array.from({ length: raresLength + 1 }, (_, index) => (
              <MenuItem key={index} value={index}>
                {index}
              </MenuItem>
            ))}
          </Select>
        </Grid>

        <Grid item xs={5}>
          <FormControlLabel
            control={
              <Switch
                checked={isDexRange}
                onChange={() => setIsDexRange((oldIsDexRange) => !oldIsDexRange)}
              />
            }
            label=''
          />
        </Grid>

        <Grid item xs={12}>
          <Stack
            spacing={2}
            direction='row'
            alignItems='center'
          >
            <Grid3x3Icon />
            <Slider
              value={listRowNum}
              onChange={(event, value) => event && setListRowNum(value as number)}
              valueLabelDisplay='auto'
              marks={marks}
              step={null}
              min={4}
              max={20}
            />
            <Grid4x4Icon />
          </Stack>
        </Grid>
      </Grid>

      <Grid container
        spacing={0.2}
        columns={listRowNum}
        sx={{ mt: 4 }}
      >
        {dexList}
      </Grid>
    </Box>
  );
};

export default DisplayDexList;
