import React, { useState, useEffect } from 'react';
import { SyncLoader } from 'react-spinners';
import {
  Dialog, DialogContent, DialogActions, Grid,
  Typography,
  Card, CardActionArea, CardMedia,
  FormControl, Select, MenuItem,
  ToggleButtonGroup, ToggleButton,
  Avatar, IconButton,
  Backdrop,
} from '@mui/material';
import {
  Image as ImageIcon,
  Bedtime as BedtimeIcon,
  Home as HomeIcon,
  CallMade as CallMadeIcon,
  CallReceived as CallReceivedIcon,
  BarChart as BarChartIcon,
  Close as CloseIcon,
} from '@mui/icons-material';

import { useStateContext } from '../context/StateContext';
import { rareDataList, typeNameList, languageList } from '../context/Config';
import RadarChart from './RadarChart';


type HandleCardClickFunction = () => void;
type DexDialogProps = {
  dexNum: number;
  rare: number;
  isDialogOpen: boolean;
  handleCardClose: HandleCardClickFunction;
}


const DexDialog: React.FC<DexDialogProps> = ({
  dexNum,
  rare,
  isDialogOpen,
  handleCardClose,
}) => {
  const {
    setMessage,
    setMessageOpen,
    setMessageSeverity,
  } = useStateContext();

  // 
  const [isRunningDialog, setIsRunningDialog] = useState<boolean>(false);
  // 
  const [name, setName] = useState<string>('');
  // 
  const [typeList, setTypeList] = useState<string[]>([]);
  // 
  const [flavorText, setFlavorText] = useState<string>('');
  // 
  const [genera, setGenera] = useState<string>('');
  // 
  const [statsList, setStatsList] = useState<number[]>([0, 0, 0, 0, 0, 0]);
  // 
  const [isShiny, setIsShiny] = useState<boolean>(false);
  // 
  const [languageCode, setLanguageCode] = useState<string>('ja');
  // 
  const [isDisplay, setIsDisplay] = useState<string>('official-artwork');


  // 
  useEffect(() => {
    (async () => {
      if (dexNum <= 0) {
        return;
      }
      
      try {
        setIsRunningDialog(true);

        // PokeAPIpokemon-species
        const speciesResponse = await fetch(`https://pokeapi.co/api/v2/pokemon-species/${dexNum}`);
        if (!speciesResponse.ok) {
          throw new Error('');
        }
        const speciesData = await speciesResponse.json();
        // 
        const languageName = speciesData.names.find((entry: any) => entry.language.name === languageCode);
        setName(languageName.name);

        // 
        const languageEntries = speciesData.flavor_text_entries.filter((entry: any) => entry.language.name === languageCode);
        // 1
        const randomLanguageEntry = languageEntries[Math.floor(Math.random() * languageEntries.length)];
        // 
        const randomFlavorText = randomLanguageEntry?.flavor_text;

        // \n\f
        const convertedText = randomFlavorText
          ? randomFlavorText.split(/[\n\f]/).map((line: string, index: number) => (
            <React.Fragment key={index}>
              {line}
              <br />
            </React.Fragment>
          ))
          : randomFlavorText;
        setFlavorText(convertedText);

        // 
        const languageGenera = speciesData.genera.find((entry: any) => entry.language.name === languageCode);
        setGenera(languageGenera?.genus);

        // PokeAPIpokemon
        const response = await fetch(`https://pokeapi.co/api/v2/pokemon/${dexNum}`);
        if (!response.ok) {
          throw new Error('');
        }
        const data = await response.json();
        // 
        const typeNameList: string[] = data.types.map((entry: any) => entry.type.name);
        setTypeList(typeNameList);

        // 
        const statNamesOrder = ['hp', 'attack', 'defense', 'speed', 'special-defense', 'special-attack'];
        const statsList: number[] = statNamesOrder.map((statName) => {
          const statData = data.stats.find((stat: any) => stat.stat.name === statName);
          return statData ? statData.base_stat : 0;
        });
        setStatsList(statsList);

      } catch (error: unknown) {
        if (error instanceof Error) {
          setMessage(error.message);
          setMessageOpen(true);
          setMessageSeverity('error');
        } else {
          setMessage('');
          setMessageOpen(true);
          setMessageSeverity('error');
        }
      } finally {
        setIsRunningDialog(false);
      }
    })()
    // eslint-disable-next-line
  }, [dexNum, languageCode]);


  // URL
  const imageUrl = (isDisplay === 'official-artwork')
    ? isShiny
      ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/official-artwork/shiny/${dexNum}.png`
      : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/official-artwork/${dexNum}.png`
    : (isDisplay === 'front')
      ? isShiny
        ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/shiny/${dexNum}.png`
        : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${dexNum}.png`
      : (isDisplay === 'back')
        ? isShiny
          ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/back/shiny/${dexNum}.png`
          : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/back/${dexNum}.png`
        : (isDisplay === 'dream-world')
          ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/dream-world/${dexNum}.svg`
          : (isDisplay === 'home')
            ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/home/${dexNum}.png`
            : isShiny
              ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/official-artwork/shiny/${dexNum}.png`
              : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/official-artwork/${dexNum}.png`;



  return (
    <Dialog
      open={isDialogOpen}
      onClose={() => handleCardClose()}
    >
      <DialogContent>
        <Grid container
          spacing={1}
          justifyContent='center'
          alignItems='center'
          sx={{ textAlign: 'center' }}
        >
          <Grid item xs={12}>
            <Typography variant='h5'>{name}</Typography>
          </Grid>
        </Grid>

        {isDisplay === 'radarChart' &&
          <RadarChart
            data={statsList}
          />
        }

        {isDisplay !== 'radarChart' &&
          <Card>
            <CardActionArea
              onClick={() => setIsShiny(!isShiny)}
              disabled={isDisplay === 'dream-world' || isDisplay === 'home'}
            >
              {dexNum > 0 &&
                <CardMedia
                  component='img'
                  image={imageUrl}
                  alt={`${dexNum}`}
                />
              }
            </CardActionArea>
          </Card>
        }

        <Grid container
          spacing={1}
          alignItems='center'
          sx={{ mt: 1, textAlign: 'center' }}
        >
          <Grid item xs={12}>
            <ToggleButtonGroup
              value={isDisplay}
              exclusive
              onChange={(event, newValue) => { setIsDisplay(newValue) }}
              color='primary'
            >
              <ToggleButton value='official-artwork'>
                <ImageIcon />
              </ToggleButton>
              <ToggleButton value='front'>
                <CallReceivedIcon />
              </ToggleButton>
              <ToggleButton value='back'>
                <CallMadeIcon />
              </ToggleButton>
              <ToggleButton value='home'>
                <HomeIcon />
              </ToggleButton>
              <ToggleButton value='dream-world'>
                <BedtimeIcon />
              </ToggleButton>
              <ToggleButton value='radarChart'>
                <BarChartIcon />
              </ToggleButton>
            </ToggleButtonGroup>
          </Grid>
        </Grid>

        <Grid container
          spacing={1}
          alignItems='center'
          sx={{ mt: 1 }}
        >
          <Grid item xs={8}>
            <FormControl>
              <Select
                value={languageCode}
                onChange={(event) => { setLanguageCode(event.target.value) }}
              >
                {languageList.map((language, index) => (
                  <MenuItem
                    key={index}
                    value={language.code}
                  >
                    {language.display}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>

          {typeList.map((type, index) => (
            <Grid item key={index} xs={2}>
              {dexNum > 0 &&
                <CardMedia
                  component='img'
                  image={`${process.env.PUBLIC_URL}/img/icon_type/icon_type_${typeNameList[type].number}.svg`}
                  alt={`${type}`}
                />
              }
            </Grid>
          ))}

          <Grid item xs={3}>
            <Typography variant='subtitle1'>No.{dexNum}</Typography>
          </Grid>
          <Grid item xs={9}>
            <Typography variant='subtitle1'>{genera}</Typography>
          </Grid>
          <Grid item xs={12}>
            <Typography variant='caption'>
              {flavorText}
            </Typography>
          </Grid>

        </Grid>
      </DialogContent>
      <DialogActions sx={{ justifyContent: 'space-between' }}>
        <Avatar
          sx={{ bgcolor: rareDataList[rare].color[500] }}
        >
          {rare}
        </Avatar>
        <IconButton
          onClick={() => handleCardClose()}
          color='inherit'
        >
          <CloseIcon />
        </IconButton>
      </DialogActions>

      <Backdrop
        open={isRunningDialog}
        sx={{
          color: 'primary.light',
          zIndex: (theme) => theme.zIndex.drawer + 1
        }}
      >
        <SyncLoader color='white' />
      </Backdrop>
    </Dialog>
  );
};

export default DexDialog;
