import React from 'react';
import {
  Box,
  Tabs, Tab,
} from '@mui/material';
import {
  Collections as CollectionsIcon,
  Rule as RuleIcon,
  Update as UpdateIcon,
} from '@mui/icons-material';

import Regulation from '../components/Regulation';
import DexList from '../components/DexList';
import Simulation from '../components/Simulation';


type HandleCardClickFunction = (dexNum: number, rare: number) => void;
type SettingsListProps = {
  handleCardClick: HandleCardClickFunction;
}

const SettingsList: React.FC<SettingsListProps> = ({
  handleCardClick,
}) => {
  const [tabValue, setTabValue] = React.useState('regulation');

  return (
    <Box>
      <Tabs
        value={tabValue}
        onChange={(event, newTabValue) => setTabValue(newTabValue)}
        indicatorColor='primary'
        textColor='primary'
        variant='fullWidth'
      >
        <Tab icon={<RuleIcon />} value='regulation' />
        <Tab icon={<CollectionsIcon />} value='dex' />
        <Tab icon={<UpdateIcon />} value='simulation' />
      </Tabs>

      {tabValue === 'regulation' &&
        <Regulation />
      }

      {tabValue === 'dex' &&
        <DexList
          handleCardClick={handleCardClick}
        />
      }

      {tabValue === 'simulation' &&
        <Simulation />
      }

    </Box>
  )
}

export default SettingsList;
