import React, { useState } from 'react';
import Chart from "react-apexcharts";
import {
  Box, Grid,
  Button, Paper,
  TableContainer, Table, TableHead, TableRow, TableCell, TableBody,
} from '@mui/material';
import {
  Update as UpdateIcon,
} from '@mui/icons-material';

import { rareDataList, truncateDecimal } from '../context/Config';
import { useStateContext } from '../context/StateContext';
import RequestUtil from '../context/RequestUtil';


type SimulationAllProps = {
  exp: number;
  battleCount: number;
}

const BattleSimulationAll: React.FC<SimulationAllProps> = ({
  exp,
  battleCount,
}) => {
  const {
    setMessage,
    setMessageOpen,
    setMessageSeverity,
    isRunning,
    setIsRunning,
    settingsList,
  } = useStateContext();
  const raresLength: number = Object.keys(settingsList.rares).length;
  const fetchPost = RequestUtil();

  // 
  const [expData, setExpData] = useState<number[][]>([[0]]);
  const [totalBattlePointData, setTotalBattlePointData] = useState<number[]>([0]);
  const [countData, setCountData] = useState<number[]>([0]);
  const [winCount, setWinCount] = useState<number[]>([0]);


  // 
  const handleBattleSimulationAllClick = async () => {
    try {
      setIsRunning(true);

      let newExpData = [];
      let newTotalBattlePointData = [];
      let newCountData = [];
      let newWinCountData = [];

      for (let i = 0; i < raresLength + 1; i++) {
        const paramsList: Record<string, string> = {
          action: 'battleSimulation',
          rare: String(i),
          exp: String(exp),
          battleCount: String(battleCount),
        };
        const returnValue = await fetchPost(paramsList);

        if (returnValue.result) {
          newExpData.push(returnValue.data.expData);
          newTotalBattlePointData.push(returnValue.data.battlePointData.pop());
          newCountData = returnValue.data.countData;
          newWinCountData.push(returnValue.data.winData.filter((entry: any) => entry === true).length);
        }
      }
      setExpData(newExpData);
      setTotalBattlePointData(newTotalBattlePointData);
      setCountData(newCountData);
      setWinCount(newWinCountData);

    } catch (error: unknown) {
      if (error instanceof Error) {
        setMessage(error.message);
        setMessageOpen(true);
        setMessageSeverity('error');
      } else {
        setMessage('');
        setMessageOpen(true);
        setMessageSeverity('error');
      }
    } finally {
      setIsRunning(false);
    }
  };


  const series = expData.map((data, index) => ({
    name: `${index}`,
    data: data,
  }));

  const options = {
    chart: {
      id: 'line-chart',
      toolbar: {
        show: false,
      },
    },
    xaxis: {
      categories: countData,
      title: {
        text: '',
      },
      tickAmount: Math.min(countData.length, 10),
      decimalsInFloat: 0,
      forceNiceScale: true,
    },
    yaxis: [
      {
        title: {
          text: '',
        },
        tickAmount: 10,
        decimalsInFloat: 2,
        forceNiceScale: true,
        min: settingsList.minExp,
        max: settingsList.maxExp,
      },
    ],
    stroke: {
      width: 2,
    },
    colors: rareDataList.map(rareData => rareData.color[500]),
  };



  return (
    <Box sx={{ mt: 2 }}>
      <Grid container
        spacing={1}
        justifyContent='center'
        alignItems='center'
        sx={{ mt: 2, textAlign: 'center' }}
      >
        <Grid item xs={12} md={6}>
          <Button
            variant='contained'
            onClick={() => handleBattleSimulationAllClick()}
            disabled={
              isRunning ||
              exp < settingsList.minExp ||
              exp > settingsList.maxExp ||
              battleCount < 1 ||
              battleCount > 999
            }
            startIcon={<UpdateIcon />}
            sx={{ width: '100%' }}
          >
            
          </Button>
        </Grid>
      </Grid>

      <Chart series={series} options={options} type='line' />

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell align='center'>
              </TableCell>
              <TableCell align='center'>
                BP
              </TableCell>
              <TableCell align='center'>
                
              </TableCell>
              <TableCell align='center'>
                
              </TableCell>
              <TableCell align='center'>
                
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody>
            {totalBattlePointData.map((totalBattlePoint, index) => (
              <TableRow key={index} sx={{ bgcolor: rareDataList[index].color[100] }}>
                <TableCell align='center'>
                  {`${index}`}
                </TableCell>
                <TableCell align='center'>
                  {totalBattlePoint}
                </TableCell>
                <TableCell align='center'>
                  {winCount[index]}
                </TableCell>
                <TableCell align='center'>
                  {(countData.length - 1) - winCount[index]}
                </TableCell>
                <TableCell align='center'>
                  {`${truncateDecimal((winCount[index] / (countData.length - 1)) * 100)}%`}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

    </Box>
  )
}

export default BattleSimulationAll;
