import React, { useState } from 'react';
import {
  Box, Grid,
  Card, CardActionArea, CardMedia,
  Paper, Badge,
  Dialog, DialogTitle, DialogContent, DialogActions, Typography, IconButton,
} from '@mui/material';
import {
  AutoAwesome as AutoAwesomeIcon,
  Close as CloseIcon,
  Attractions as AttractionsIcon,
  SportsMma as SportsMmaIcon,
} from '@mui/icons-material';

import { useStateContext } from '../context/StateContext';
import { rareDataList, truncateDecimal } from '../context/Config';


type SetSelectNumFunction = (selectNum: number) => void;
type SetIsUserInfoDialogOpenFunction = (isUserInfoDialogOpen: boolean) => void;
type HandleCardClickFunction = (dexNum: number, rare: number) => void;
type UserInfoProps = {
  display: string;
  initialize: boolean;
  isUpdate: boolean;
  isRankingBonus: boolean;
  addGachaCount: number;
  addPokeExp: number;
  selectBattleNum: number;
  selectNum: number;
  setSelectNum: SetSelectNumFunction;
  isUserInfoDialogOpen: boolean;
  setIsUserInfoDialogOpen: SetIsUserInfoDialogOpenFunction;
  handleCardClick: HandleCardClickFunction;
}

const UserInfo: React.FC<UserInfoProps> = ({
  display,
  initialize,
  isUpdate,
  isRankingBonus,
  addGachaCount,
  addPokeExp,
  selectBattleNum,
  selectNum,
  setSelectNum,
  isUserInfoDialogOpen,
  setIsUserInfoDialogOpen,
  handleCardClick,
}) => {
  const {
    userInfo,
    settingsList,
  } = useStateContext();
  const pokeList = userInfo.pokeList;
  const gachaCount = userInfo.gachaCount;
  const battlePoint = userInfo.battlePoint;

  

  return (
    <Box>
      <Grid container
        spacing={1}
        justifyContent='space-around'
        alignItems='center'
        sx={{ mt: 1, textAlign: 'center' }}
      >
        <Grid item xs={5}>
          <Badge
            badgeContent={addGachaCount}
            max={999}
            color='primary'
            sx={{ width: '100%' }}
          >
            <Paper elevation={display === 'gacha' ? 4 : 1} sx={{ width: '100%' }}>
              <Grid container alignItems='center'>
                <Grid item xs={3} md={12}>
                  <AttractionsIcon color={display === 'gacha' ? 'primary' : 'inherit'} />
                </Grid>
                <Grid item xs={9} md={12}>
                  <Typography variant='h6' color={display === 'gacha' ? 'primary' : 'inherit'}>
                    {gachaCount}
                  </Typography>
                </Grid>
              </Grid>
            </Paper>
          </Badge>
        </Grid>
        <Grid item xs={5}>
          <Paper elevation={display === 'battle' ? 4 : 1}>
            <Grid container alignItems='center'>
              <Grid item xs={3} md={12}>
                <SportsMmaIcon color={display === 'battle' ? 'primary' : 'inherit'} />
              </Grid>
              <Grid item xs={9} md={12}>
                <Typography variant='h6' color={display === 'battle' ? 'primary' : 'inherit'}>
                  {battlePoint}BP
                </Typography>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
      </Grid>

      <Grid container
        spacing={1}
        justifyContent='center'
        alignItems='center'
        sx={{ mt: 1, textAlign: 'center' }}
      >
        {pokeList.map((poke: any, index: number) => (
          <Grid item
            key={index}
            xs={4} md={2}
          >
            <Card
              sx={{
                bgcolor: display === 'gacha' || selectNum !== (index + 1) ? rareDataList[poke.rare].color[100] : rareDataList[poke.rare].color[400],
                border: display === 'gacha' || selectNum !== (index + 1) ? 'none' : 'solid',
                borderColor: 'primary.main',
                height: '100%'
              }}
            >
              <CardActionArea
                onClick={display === 'gacha'
                  ? () => handleCardClick(poke.dexNum, poke.rare)
                  : () => setSelectNum(index + 1)
                }
                disabled={poke.dexNum <= 0}
                sx={{ height: '100%' }}
              >
                <Box
                  sx={{
                    aspectRatio: '1 / 1',
                    height: '100%',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                  }}
                >
                  {poke.dexNum > 0 &&
                    <CardMedia
                      component='img'
                      image={poke.shiny
                        ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/shiny/${poke.dexNum}.gif`
                        : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/${poke.dexNum}.gif`
                      }
                      alt={`${index}`}
                      sx={{
                        maxWidth: '100%',
                        maxHeight: '100%',
                        width: 'auto',
                        height: 'auto',
                        objectFit: 'contain',
                      }}
                    />
                  }
                </Box>

                <Grid container
                  alignItems='center'
                  sx={{ mt: 1 }}
                >
                  <Grid item xs={2}>
                    {poke.shiny &&
                      <AutoAwesomeIcon sx={{ fontSize: 'inherit', ml: 0.75 }} />
                    }
                  </Grid>
                  <Grid item xs={8}>
                    <Badge
                      badgeContent={((selectBattleNum === -1) || ((selectBattleNum - 1) === index)) ? addPokeExp : 0}
                      max={settingsList.maxExp}
                      color='primary'
                      sx={{ width: '80%' }}
                    >
                      <Paper
                        elevation={0}
                        sx={{
                          bgcolor: rareDataList[poke.rare].color[400],
                          width: '100%'
                        }}
                      >
                        <Typography
                          variant='subtitle2'
                          color='white'
                          sx={{
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center'
                          }}
                        >
                          {truncateDecimal(poke.exp)}
                        </Typography>
                      </Paper>
                    </Badge>
                  </Grid>
                </Grid>

              </CardActionArea>
            </Card>
          </Grid>
        ))}
      </Grid>

      <Dialog
        onClose={() => setIsUserInfoDialogOpen(false)}
        open={isUserInfoDialogOpen && (initialize || isRankingBonus)}
        sx={{ textAlign: 'center' }}
      >
        <DialogTitle></DialogTitle>

        {initialize &&
          <DialogContent>
            <Typography>
              {settingsList.partyTotalNum}
            </Typography>
            <Typography>
              
              
            </Typography>
          </DialogContent>
        }
        {isRankingBonus &&
          <DialogContent>
            <Typography>
              
              {settingsList.bonusGachaCount}
            </Typography>
          </DialogContent>
        }

        <DialogActions sx={{ justifyContent: 'center' }}>
          <IconButton
            onClick={() => setIsUserInfoDialogOpen(false)}
            color='inherit'
          >
            <CloseIcon />
          </IconButton>
        </DialogActions>
      </Dialog>
    </Box>
  )
}

export default UserInfo;
