import React, { useState, useEffect } from 'react';
import { HashLoader } from 'react-spinners';
import {
  Box, Container,
  AppBar, Toolbar, IconButton,
  Snackbar, Alert, AlertTitle,
  Backdrop,
} from '@mui/material';
import {
  Attractions as AttractionsIcon,
  SportsMma as SportsMmaIcon,
  ChangeCircle as ChangeCircleIcon,
  FormatListNumbered as FormatListNumberedIcon,
  Balance as BalanceIcon,
  Home as HomeIcon,
} from '@mui/icons-material';

import { useStateContext } from '../context/StateContext';
import { truncateDecimal } from '../context/Config';
import RequestUtil from '../context/RequestUtil';
import UserInfo from '../components/UserInfo';
import DexDialog from '../components/DexDialog';
import Gacha from './Gacha';
import Battle from './Battle';
import SelectWild from './SelectWild';
import Ranking from './Ranking';
import SettingsList from './SettingsList';
import GachaAnimation from '../animation/GachaAnimation';
import BattleAnimation from '../animation/BattleAnimation';
import SelectWildAnimation from '../animation/SelectWildAnimation';



function Main() {
  const {
    message,
    setMessage,
    messageOpen,
    setMessageOpen,
    messageSeverity,
    setMessageSeverity,
    isRunning,
    setIsRunning,
    setUserInfo,
    setSettingsList,
  } = useStateContext();
  const fetchPost = RequestUtil();


  // useEffect
  const [isFirst, setIsFirst] = useState<boolean>(true);
  // 
  const [display, setDisplay] = useState<string>('gacha');
  // 
  const [initialize, setInitialize] = useState<boolean>(false);
  // 
  const [isUpdate, setIsUpdate] = useState<boolean>(false);
  // 
  const [isRankingBonus, setIsRankingBonus] = useState<boolean>(false);
  // 
  const [addGachaCount, setAddGachaCount] = useState<number>(0);
  // 
  const [addPokeExp, setAddPokeExp] = useState<number>(0);
  // -1
  const [selectBattleNum, setSelectBattleNum] = useState<number>(-1);
  // 
  const [selectNum, setSelectNum] = useState<number>(1);
  // 
  const [selectedDexNum, setSelectedDexNum] = useState<number>(0);
  // 
  const [selectedRare, setSelectedRare] = useState<number>(0);
  // 
  const [isDialogOpen, setIsDialogOpen] = useState<boolean>(false);
  // 
  const [isUserInfoDialogOpen, setIsUserInfoDialogOpen] = useState<boolean>(false);
  // 
  const [openGachaAnimation, setOpenGachaAnimation] = useState<boolean>(false);
  const [gachaType, setGachaType] = useState<string>('normal');
  // 
  const [openBattleAnimation, setOpenBattleAnimation] = useState<boolean>(false);
  const [win, setWin] = useState<boolean>(false);
  // 
  const [openSelectWildAnimation, setOpenSelectWildAnimation] = useState<boolean>(false);
  const [lastSelectPoke, setLastSelectPoke] = useState<any>({
    dexNum: 0,
    shiny: false,
    exp: 0,
    rare: 0,
  });
  const [lastWildPoke, setLastWildPoke] = useState<any>({
    dexNum: 0,
    shiny: false,
    exp: 0,
    rare: 0,
  });



  // 
  useEffect(() => {
    (async () => {
      // 
      if (!isFirst) {
        return;
      }
      setIsFirst(false);

      try {
        setIsRunning(true);

        const paramsList: Record<string, string> = {
          action: 'userInfo',
        };
        const returnValue = await fetchPost(paramsList);

        if (returnValue.result) {
          setInitialize(returnValue.data.initialize);
          setIsUpdate(returnValue.data.isUpdate);
          setIsRankingBonus(returnValue.data.isRankingBonus);
          setUserInfo(returnValue.data.userInfo);
          setSettingsList(returnValue.data.settingsList);
          setAddGachaCount(truncateDecimal(returnValue.data.addGachaCount));
          setAddPokeExp(truncateDecimal(returnValue.data.addPokeExp));
          setIsUserInfoDialogOpen(true);
        } else {
          window.location.href = 'https://MaitakeTeikoku.github.io/MaitakeAuth';
        }

      } catch (error: unknown) {
        if (error instanceof Error) {
          setMessage(error.message);
          setMessageOpen(true);
          setMessageSeverity('error');
        } else {
          setMessage('');
          setMessageOpen(true);
          setMessageSeverity('error');
        }
        window.location.href = 'https://MaitakeTeikoku.github.io/MaitakeAuth';
      } finally {
        setIsRunning(false);
      }
    })()
    // eslint-disable-next-line
  }, []);


  // 
  const handleCardClick = (dexNum: number, rare: number) => {
    setSelectedDexNum(dexNum);
    setSelectedRare(rare);
    setIsDialogOpen(true);
  };

  // 
  const handleCardClose = () => {
    setIsDialogOpen(false);
    setSelectedDexNum(0);
    setSelectedRare(0);
  };


  const appBarList = [
    {
      name: 'gacha',
      icon: <AttractionsIcon />,
    }, {
      name: 'battle',
      icon: <SportsMmaIcon />,
    }, {
      name: 'selectWild',
      icon: <ChangeCircleIcon />,
    }, {
      name: 'ranking',
      icon: <FormatListNumberedIcon />,
    }, {
      name: 'settingsList',
      icon: <BalanceIcon />,
    },
  ];



  return (
    <Box sx={{ mb: 2 }}>
      <AppBar position='static'>
        <Toolbar
          sx={{
            display: 'flex',
            justifyContent: 'space-between'
          }}
        >
          {appBarList.map((select) => (
            <IconButton
              key={select.name}
              color='inherit'
              onClick={() => setDisplay(select.name)}
              disabled={isRunning || display === select.name}
            >
              {select.icon}
            </IconButton>
          ))}

          <IconButton
            color='inherit'
            onClick={() => {
              window.location.href = 'https://MaitakeTeikoku.github.io/MaitakeAuth';
            }}
          >
            <HomeIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      <Container component='main' fixed>
        {(display === 'gacha' || display === 'battle' || display === 'selectWild') &&
          <UserInfo
            display={display}
            initialize={initialize}
            isUpdate={isUpdate}
            isRankingBonus={isRankingBonus}
            addGachaCount={addGachaCount}
            addPokeExp={addPokeExp}
            selectBattleNum={selectBattleNum}
            selectNum={selectNum}
            setSelectNum={setSelectNum}
            isUserInfoDialogOpen={isUserInfoDialogOpen}
            setIsUserInfoDialogOpen={setIsUserInfoDialogOpen}
            handleCardClick={handleCardClick}
          />
        }

        {display === 'gacha' &&
          <Gacha
            setDisplay={setDisplay}
            setAddGachaCount={setAddGachaCount}
            setAddPokeExp={setAddPokeExp}
            setOpenGachaAnimation={setOpenGachaAnimation}
            setGachaType={setGachaType}
          />
        }

        {display === 'battle' &&
          <Battle
            selectNum={selectNum}
            setDisplay={setDisplay}
            setAddGachaCount={setAddGachaCount}
            setAddPokeExp={setAddPokeExp}
            setSelectBattleNum={setSelectBattleNum}
            setOpenBattleAnimation={setOpenBattleAnimation}
            setLastSelectPoke={setLastSelectPoke}
            setWin={setWin}
          />
        }

        {display === 'selectWild' &&
          <SelectWild
            selectNum={selectNum}
            setDisplay={setDisplay}
            setAddGachaCount={setAddGachaCount}
            setAddPokeExp={setAddPokeExp}
            handleCardClick={handleCardClick}
            setOpenSelectWildAnimation={setOpenSelectWildAnimation}
            setLastSelectPoke={setLastSelectPoke}
            setLastWildPoke={setLastWildPoke}
          />
        }

        {display === 'ranking' &&
          <Ranking
            handleCardClick={handleCardClick}
          />
        }

        {display === 'settingsList' &&
          <SettingsList
            handleCardClick={handleCardClick}
          />
        }

      </Container>

      {isDialogOpen &&
        <DexDialog
          dexNum={selectedDexNum}
          rare={selectedRare}
          isDialogOpen={isDialogOpen}
          handleCardClose={handleCardClose}
        />
      }

      {openGachaAnimation &&
        <GachaAnimation
          openGachaAnimation={openGachaAnimation}
          setOpenGachaAnimation={setOpenGachaAnimation}
          gachaType={gachaType}
        />
      }

      {openBattleAnimation &&
        <BattleAnimation
          selectNum={selectNum}
          addPokeExp={addPokeExp}
          openBattleAnimation={openBattleAnimation}
          setOpenBattleAnimation={setOpenBattleAnimation}
          lastSelectPoke={lastSelectPoke}
          win={win}
        />
      }

      {openSelectWildAnimation &&
        <SelectWildAnimation
          openSelectWildAnimation={openSelectWildAnimation}
          setOpenSelectWildAnimation={setOpenSelectWildAnimation}
          lastSelectPoke={lastSelectPoke}
          lastWildPoke={lastWildPoke}
        />
      }

      <Snackbar
        message={message}
        open={messageOpen}
        autoHideDuration={
          (messageSeverity === 'error' || messageSeverity === 'warning')
            ? null
            : 6000
        }
        onClose={() => setMessageOpen(false)}
      >
        <Alert
          severity={messageSeverity}
          onClose={() => setMessageOpen(false)}
        >
          <AlertTitle>
            {messageSeverity.toUpperCase()}
          </AlertTitle>
          {message}
        </Alert>
      </Snackbar>

      <Backdrop
        open={isRunning}
        sx={{
          color: 'primary.light',
          zIndex: (theme) => theme.zIndex.drawer + 1
        }}
      >
        <HashLoader color='white' />
      </Backdrop>
    </Box>
  );
}

export default Main;
