import React, { useState } from 'react';
import {
  Box, Grid,
  Button,
  Card, CardActionArea, CardMedia,
  Dialog, DialogTitle, DialogContent, DialogActions, IconButton,
  Badge, Paper, Typography,
} from '@mui/material';
import {
  AutoAwesome as AutoAwesomeIcon,
  Attractions as AttractionsIcon,
  SportsMma as SportsMmaIcon,
  ChangeCircle as ChangeCircleIcon,
  ArrowForward as ArrowForwardIcon,
  Check as CheckIcon,
  Close as CloseIcon,
} from '@mui/icons-material';

import { useStateContext } from '../context/StateContext';
import { rareDataList } from '../context/Config';
import RequestUtil from '../context/RequestUtil';



type SetDisplayFunction = (display: string) => void;
type HandleCardClickFunction = (dexNum: number, rare: number) => void;
type SetAddGachaCountFunction = (addGachaCount: number) => void;
type SetAddPokeExpFunction = (addPokeExp: number) => void;
type setOpenSelectWildAnimationFunction = (openSelectWildAnimation: boolean) => void;
type setLastSelectPokeFunction = (lastSelectPoke: any) => void;
type setLastWildPokeFunction = (lastWildPoke: any) => void;
type SelectWildProps = {
  selectNum: number;
  setDisplay: SetDisplayFunction;
  setAddGachaCount: SetAddGachaCountFunction;
  setAddPokeExp: SetAddPokeExpFunction;
  handleCardClick: HandleCardClickFunction;
  setOpenSelectWildAnimation: setOpenSelectWildAnimationFunction;
  setLastSelectPoke: setLastSelectPokeFunction;
  setLastWildPoke: setLastWildPokeFunction;
}

const SelectWild: React.FC<SelectWildProps> = ({
  selectNum,
  setDisplay,
  setAddGachaCount,
  setAddPokeExp,
  handleCardClick,
  setOpenSelectWildAnimation,
  setLastSelectPoke,
  setLastWildPoke,
}) => {
  const {
    setMessage,
    setMessageOpen,
    setMessageSeverity,
    isRunning,
    setIsRunning,
    userInfo,
    setUserInfo,
    settingsList
  } = useStateContext();
  const fetchPost = RequestUtil();

  const [openDialog, setOpenDialog] = useState<boolean>(false);


  // 
  const handleSelectWildClick = async () => {
    try {
      setIsRunning(true);
      setOpenDialog(false);
      setAddGachaCount(0);
      setAddPokeExp(0);
      setLastSelectPoke({
        dexNum: userInfo.pokeList[selectNum - 1].dexNum,
        shiny: userInfo.pokeList[selectNum - 1].shiny,
        exp: userInfo.pokeList[selectNum - 1].exp,
        rare: userInfo.pokeList[selectNum - 1].rare,
      });
      setLastWildPoke({
        dexNum: userInfo.wild_dexNum,
        shiny: userInfo.wild_shiny,
        exp: settingsList.initialPokeExp,
        rare: userInfo.wild_rare,
      });
      setOpenSelectWildAnimation(true);

      const paramsList: Record<string, string> = {
        action: 'selectWild',
        selectNum: String(selectNum),
      };
      const returnValue = await fetchPost(paramsList);

      if (returnValue.result) {
        setUserInfo(returnValue.data.userInfo);
      }

    } catch (error: unknown) {
      if (error instanceof Error) {
        setMessage(error.message);
        setMessageOpen(true);
        setMessageSeverity('error');
      } else {
        setMessage('');
        setMessageOpen(true);
        setMessageSeverity('error');
      }
    } finally {
      setIsRunning(false);
    }
  };



  return (
    <Box>
      <Grid container
        spacing={2}
        justifyContent='center'
        alignItems='center'
        sx={{ mt: 2, textAlign: 'center' }}
      >
        <Grid item xs={12} md={4}>
          <Button
            variant='contained'
            onClick={() => setOpenDialog(true)}
            disabled={isRunning || !userInfo.wildCanSelect || userInfo.wild_dexNum <= 0}
            startIcon={<ChangeCircleIcon />}
            sx={{ width: '100%' }}
          >
            
          </Button>
        </Grid>
      </Grid>

      <Grid container
        spacing={1}
        justifyContent='center'
        alignItems='center'
        sx={{ mt: 1, textAlign: 'center' }}
      >
        <Grid item
          xs={4} md={2}
        >
          <Card
            sx={{ bgcolor: rareDataList[userInfo.wild_rare].color[100], height: '100%' }}
          >
            <CardActionArea
              onClick={() => handleCardClick(userInfo.wild_dexNum, userInfo.wild_rare)}
              disabled={userInfo.wild_dexNum <= 0}
              sx={{ height: '100%' }}
            >
              <Box
                sx={{
                  aspectRatio: '1 / 1',
                  height: '100%',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                }}
              >
                {userInfo.wild_dexNum > 0 &&
                  <CardMedia
                    component='img'
                    image={userInfo.wild_shiny
                      ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/shiny/${userInfo.wild_dexNum}.gif`
                      : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/${userInfo.wild_dexNum}.gif`
                    }
                    alt={`${userInfo.wild_dexNum}`}
                    sx={{
                      maxWidth: '100%',
                      maxHeight: '100%',
                      width: 'auto',
                      height: 'auto',
                      objectFit: 'contain',
                    }}
                  />
                }
              </Box>

              <Grid container
                alignItems='center'
                sx={{ mt: 1 }}
              >
                <Grid item xs={2}>
                  {userInfo.wild_shiny &&
                    <AutoAwesomeIcon sx={{ fontSize: 'inherit', ml: 0.75 }} />
                  }
                </Grid>
                <Grid item xs={8}>
                  <Badge
                    badgeContent={0}
                    max={settingsList.maxExp}
                    color='primary'
                    sx={{ width: '80%' }}
                  >
                    <Paper
                      elevation={0}
                      sx={{
                        bgcolor: rareDataList[userInfo.wild_rare].color[400],
                        width: '100%'
                      }}
                    >
                      <Typography
                        variant='subtitle2'
                        color='white'
                        sx={{
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'center'
                        }}
                      >
                        {settingsList.initialPokeExp}
                      </Typography>
                    </Paper>
                  </Badge>
                </Grid>
              </Grid>
            </CardActionArea>
          </Card>
        </Grid>

        <Grid container
          spacing={1}
          justifyContent='center'
          alignItems='center'
          sx={{ mt: 2, textAlign: 'center' }}
        >
          <Grid item xs={6}>
            <Button
              variant='outlined'
              onClick={() => setDisplay('gacha')}
              disabled={isRunning}
              startIcon={<AttractionsIcon />}
            >
              
            </Button>
          </Grid>
          <Grid item xs={6}>
            <Button
              variant='outlined'
              onClick={() => setDisplay('battle')}
              disabled={isRunning}
              startIcon={<SportsMmaIcon />}
            >
              
            </Button>
          </Grid>
        </Grid>
      </Grid>

      <Dialog
        onClose={() => setOpenDialog(false)}
        open={openDialog}
        sx={{ textAlign: 'center' }}
      >
        <DialogTitle></DialogTitle>
        <DialogContent>
          <Grid container
            spacing={1}
            alignItems='center'
            sx={{ mt: 1, textAlign: 'center' }}
          >
            <Grid item xs={5}>
              <CardMedia
                component='img'
                image={userInfo.pokeList[selectNum - 1].shiny
                  ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/shiny/${userInfo.pokeList[selectNum - 1].dexNum}.png`
                  : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${userInfo.pokeList[selectNum - 1].dexNum}.png`
                }
                alt={`${userInfo.wild_dexNum}`}
              />
            </Grid>
            <Grid item xs={2}>
              <ArrowForwardIcon />
            </Grid>
            <Grid item xs={5}>
              <CardMedia
                component='img'
                image={userInfo.wild_shiny
                  ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/shiny/${userInfo.wild_dexNum}.png`
                  : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${userInfo.wild_dexNum}.png`
                }
                alt={`${userInfo.wild_dexNum}`}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions sx={{ justifyContent: 'space-around' }}>
          <IconButton
            onClick={() => setOpenDialog(false)}
            color='error'
          >
            <CloseIcon />
          </IconButton>
          <IconButton
            onClick={() => handleSelectWildClick()}
            color='success'
          >
            <CheckIcon />
          </IconButton>
        </DialogActions>
      </Dialog>
    </Box>
  )
}

export default SelectWild;
