import React from 'react';
import {
  Box, Grid,
  Button,
} from '@mui/material';
import {
  SportsMma as SportsMmaIcon,
} from '@mui/icons-material';

import { useStateContext } from '../context/StateContext';
import { truncateDecimal } from '../context/Config';
import RequestUtil from '../context/RequestUtil';



type SetDisplayFunction = (display: string) => void;
type SetAddGachaCountFunction = (addGachaCount: number) => void;
type SetAddPokeExpFunction = (addPokeExp: number) => void;
type SetSelectBattleNumFunction = (SelectBattleNum: number) => void;
type SetOpenBattleAnimationFunction = (openBattleAnimation: boolean) => void;
type setLastSelectPokeFunction = (lastSelectPoke: any) => void;
type SetWinFunction = (win: boolean) => void;
type BattleProps = {
  selectNum: number;
  setDisplay: SetDisplayFunction;
  setAddGachaCount: SetAddGachaCountFunction;
  setAddPokeExp: SetAddPokeExpFunction;
  setSelectBattleNum: SetSelectBattleNumFunction;
  setOpenBattleAnimation: SetOpenBattleAnimationFunction;
  setLastSelectPoke: setLastSelectPokeFunction;
  setWin: SetWinFunction;
}

const Battle: React.FC<BattleProps> = ({
  selectNum,
  setDisplay,
  setAddGachaCount,
  setAddPokeExp,
  setSelectBattleNum,
  setOpenBattleAnimation,
  setLastSelectPoke,
  setWin,
}) => {
  const {
    setMessage,
    setMessageOpen,
    setMessageSeverity,
    isRunning,
    setIsRunning,
    userInfo,
    setUserInfo,
    settingsList,
  } = useStateContext();
  const fetchPost = RequestUtil();


  // 
  const handleBattleClick = async () => {
    try {
      setIsRunning(true);
      setAddGachaCount(0);
      setAddPokeExp(0);
      setLastSelectPoke({
        dexNum: userInfo.pokeList[selectNum - 1].dexNum,
        shiny: userInfo.pokeList[selectNum - 1].shiny,
        exp: userInfo.pokeList[selectNum - 1].exp,
        rare: userInfo.pokeList[selectNum - 1].rare,
      });

      const paramsList: Record<string, string> = {
        action: 'battle',
        selectNum: String(selectNum),
      };
      const returnValue = await fetchPost(paramsList);

      
      if (returnValue.result) {
        setUserInfo(returnValue.data.userInfo);
        setAddPokeExp(truncateDecimal(returnValue.data.addPokeExp));
        setSelectBattleNum(selectNum);
        setWin(returnValue.data.win);
        setOpenBattleAnimation(true);

        if (returnValue.data.userInfo.wildCanSelect) {
          setDisplay('selectWild');
          setAddGachaCount(settingsList.winGachaCount);
        }
      }

    } catch (error: unknown) {
      if (error instanceof Error) {
        setMessage(error.message);
        setMessageOpen(true);
        setMessageSeverity('error');
      } else {
        setMessage('');
        setMessageOpen(true);
        setMessageSeverity('error');
      }
    } finally {
      setIsRunning(false);
    }
  };



  return (
    <Box>
      <Grid container
        spacing={2}
        justifyContent='center'
        alignItems='center'
        sx={{ mt: 2, textAlign: 'center' }}
      >
        <Grid item xs={12} md={4}>
          <Button
            variant='contained'
            onClick={() => handleBattleClick()}
            disabled={isRunning || userInfo.battlePoint < Math.ceil((userInfo.pokeList[selectNum - 1].rare + 1) * userInfo.pokeList[selectNum - 1].exp)}
            startIcon={<SportsMmaIcon />}
            sx={{ width: '100%' }}
            >
            {Math.ceil((userInfo.pokeList[selectNum - 1].rare + 1) * userInfo.pokeList[selectNum - 1].exp)}BP
          </Button>
        </Grid>
      </Grid>
    </Box>
  )
}

export default Battle;
