import { createContext, useContext, useState } from 'react';



type Severity = 'error' | 'warning' | 'info' | 'success';

type State = {
  message: string;
  setMessage: React.Dispatch<React.SetStateAction<string>>;
  messageOpen: boolean;
  setMessageOpen: React.Dispatch<React.SetStateAction<boolean>>;
  messageSeverity: Severity;
  setMessageSeverity: React.Dispatch<React.SetStateAction<Severity>>;
  isRunning: boolean;
  setIsRunning: React.Dispatch<React.SetStateAction<boolean>>;
  userInfo: any;
  setUserInfo: React.Dispatch<React.SetStateAction<any>>;
  settingsList: any;
  setSettingsList: React.Dispatch<React.SetStateAction<any>>;
}
type StateProviderProps = {
  children: React.ReactNode;
}


const StateContext = createContext<State>({
  message: '',
  setMessage: () => { },
  messageOpen: false,
  setMessageOpen: () => { },
  messageSeverity: 'info',
  setMessageSeverity: () => { },
  isRunning: false,
  setIsRunning: () => { },
  userInfo: {},
  setUserInfo: () => { },
  settingsList: {},
  setSettingsList: () => { },
});

export const StateProvider: React.FC<StateProviderProps> = ({ children }) => {
  const [message, setMessage] = useState<string>('');
  const [messageOpen, setMessageOpen] = useState<boolean>(false);
  const [messageSeverity, setMessageSeverity] = useState<Severity>('info');
  const [isRunning, setIsRunning] = useState<boolean>(false);
  const [userInfo, setUserInfo] = useState<any>({
    "userName": "",
    "lastDate": "",
    "runningDays": 0,
    "gachaCount": 0,
    "battlePoint": 0,
    "gachaTotal": 0,
    "battleTotal": 0,
    "wild_dexNum": 0,
    "wild_shiny": false,
    "wild_rare": 0,
    "pokeList": [
      {
        "dexNum": 0,
        "shiny": false,
        "exp": 0,
        "rare": 0
      }, {
        "dexNum": 0,
        "shiny": false,
        "exp": 0,
        "rare": 0
      }, {
        "dexNum": 0,
        "shiny": false,
        "exp": 0,
        "rare": 0
      }, {
        "dexNum": 0,
        "shiny": false,
        "exp": 0,
        "rare": 0
      }, {
        "dexNum": 0,
        "shiny": false,
        "exp": 0,
        "rare": 0
      }, {
        "dexNum": 0,
        "shiny": false,
        "exp": 0,
        "rare": 0
      }
    ]
  });
  const [settingsList, setSettingsList] = useState<any>({
    "dexMax": 649,
    "dexRange": 649,
    "partyTotalNum": 6,
    "rares": {
      "1": {
        "dexNumList": [],
        "probability": 0,
        "probabilityPremier": 0,
        "probabilityLuxury": 0
      },
      "2": {
        "dexNumList": [],
        "probability": 0,
        "probabilityPremier": 0,
        "probabilityLuxury": 0
      },
      "3": {
        "dexNumList": [],
        "probability": 0,
        "probabilityPremier": 0,
        "probabilityLuxury": 0
      },
      "4": {
        "dexNumList": [],
        "probability": 0,
        "probabilityPremier": 0,
        "probabilityLuxury": 0
      }
    },
    "shinyProbability": 0,
    "shinyPoint": 0,
    "initialPokeExp": 0,
    "addExp": 0,
    "minExp": 0,
    "maxExp": 0,
    "winExpRatio": 0,
    "initialGachaCount": 0,
    "bonusGachaCount": 0,
    "winGachaCount": 0,
    "normalGachaCost": 0,
    "premierGachaCost": 0,
    "luxuryGachaCost": 0,
    "initialBattlePoint": 0
  });


  const state = {
    message,
    setMessage,
    messageOpen,
    setMessageOpen,
    messageSeverity,
    setMessageSeverity,
    isRunning,
    setIsRunning,
    userInfo,
    setUserInfo,
    settingsList,
    setSettingsList,
  };

  return (
    <StateContext.Provider value={state}>
      {children}
    </StateContext.Provider>
  );
};

export const useStateContext = () => {
  return useContext(StateContext);
};
