import React from 'react';
import {
  Box,
  Accordion, AccordionSummary, AccordionDetails,
  TableContainer, Table, TableRow, TableCell, TableBody,
  Paper, Typography,
} from '@mui/material';
import {
  ExpandMore as ExpandMoreIcon,
  CatchingPokemon as CatchingPokemonIcon,
  CalendarMonth as CalendarMonthIcon,
  Attractions as AttractionsIcon,
  SportsMma as SportsMmaIcon,
  FormatListNumbered as FormatListNumberedIcon,
} from '@mui/icons-material';

import { rareDataList, gachaTypeList, truncateDecimal } from '../context/Config';
import { useStateContext } from '../context/StateContext';



const Regulation: React.FC = () => {
  const {
    settingsList,
  } = useStateContext();
  const raresLength: number = Object.keys(settingsList.rares).length;



  return (
    <Box sx={{ mt: 2 }}>
      <Accordion sx={{ color: 'white', bgcolor: 'primary.main'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon sx={{ color: 'white' }} />}
        >
          <CatchingPokemonIcon />
          <Typography sx={{ ml: 2 }}>
            
          </Typography>
        </AccordionSummary>

        <AccordionDetails>
          <TableContainer component={Paper}>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.dexRange}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.partyTotalNum}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {`${truncateDecimal(settingsList.shinyProbability * 100)}%`}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.initialPokeExp}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.minExp}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.maxExp}
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </AccordionDetails>
      </Accordion>

      <Accordion sx={{ color: 'white', bgcolor: 'primary.main'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon sx={{ color: 'white' }} />}
        >
          <CalendarMonthIcon />
          <Typography sx={{ ml: 2 }}>
            
          </Typography>
        </AccordionSummary>

        <AccordionDetails>
          <TableContainer component={Paper}>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {`+(${settingsList.initialGachaCount - 1} + )`}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    BP
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.initialBattlePoint}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {`+${settingsList.addExp}`}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    1
                  </TableCell>
                  <TableCell align='center'>
                    {`+${settingsList.bonusGachaCount}`}
                  </TableCell>
                </TableRow>

              </TableBody>
            </Table>
          </TableContainer>
        </AccordionDetails>
      </Accordion>

      <Accordion sx={{ color: 'white', bgcolor: 'primary.main'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon sx={{ color: 'white' }} />}
        >
          <AttractionsIcon />
          <Typography sx={{ ml: 2 }}>
            
          </Typography>
        </AccordionSummary>

        <AccordionDetails>
          <TableContainer component={Paper}>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell align='center'>
                  </TableCell>
                  {gachaTypeList.map((gacha) => (
                    <TableCell align='center'
                      key={gacha.id}
                    >
                      <img
                        src={gacha.url}
                        alt={gacha.id}
                      />
                    </TableCell>
                  ))}
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.normalGachaCost}
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.premierGachaCost}
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.luxuryGachaCost}
                  </TableCell>
                </TableRow>

                {Array.from({ length: raresLength }, (_, index) => (
                  <TableRow key={index + 1} sx={{ bgcolor: rareDataList[index + 1].color[100] }}>
                    <TableCell align='center'>
                      {`${index + 1}`}
                    </TableCell>
                    <TableCell align='center'>
                      {`${truncateDecimal(settingsList.rares[String(index + 1)].probability * 100)}%`}
                    </TableCell>
                    <TableCell align='center'>
                      {`${truncateDecimal(settingsList.rares[String(index + 1)].probabilityPremier * 100)}%`}
                    </TableCell>
                    <TableCell align='center'>
                      {`${truncateDecimal(settingsList.rares[String(index + 1)].probabilityLuxury * 100)}%`}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </AccordionDetails>
      </Accordion>

      <Accordion sx={{ color: 'white', bgcolor: 'primary.main'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon sx={{ color: 'white' }} />}
        >
          <SportsMmaIcon />
          <Typography sx={{ ml: 2 }}>
            
          </Typography>
        </AccordionSummary>

        <AccordionDetails>
          <TableContainer component={Paper} sx={{ mt: 2 }}>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell align='center'>
                    BP
                  </TableCell>
                  <TableCell align='center'>
                    {'( + 1) x '}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.initialPokeExp}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {`+${settingsList.winGachaCount}`}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    
                  </TableCell>
                </TableRow>

              </TableBody>
            </Table>
          </TableContainer>
        </AccordionDetails>
      </Accordion>

      <Accordion sx={{ color: 'white', bgcolor: 'primary.main'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon sx={{ color: 'white' }} />}
        >
          <FormatListNumberedIcon />
          <Typography sx={{ ml: 2 }}>
            
          </Typography>
        </AccordionSummary>

        <AccordionDetails>
          <TableContainer component={Paper} sx={{ mt: 2 }}>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {'( + 1) x '}
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell align='center'>
                    
                  </TableCell>
                  <TableCell align='center'>
                    {settingsList.shinyPoint}
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </AccordionDetails>
      </Accordion>
    </Box>
  )
}

export default Regulation;
