import React, { useState, useEffect, useRef } from 'react';
import { motion, useAnimation, animate } from 'framer-motion';
import {
  Box, Container,
  Dialog, DialogContent, IconButton,
  Snackbar, SnackbarContent,
} from '@mui/material';
import {
  Close as CloseIcon,
  ArrowDropDown as ArrowDropDownIcon,
} from '@mui/icons-material';

import { useStateContext } from '../context/StateContext';
import { rareDataList, sleep, convertColorcode } from '../context/Config';


type SetOpenGachaAnimationFunction = (animation: boolean) => void;
type GachaAnimationProps = {
  openGachaAnimation: boolean;
  setOpenGachaAnimation: SetOpenGachaAnimationFunction;
  gachaType: string;
}

const GachaAnimation: React.FC<GachaAnimationProps> = ({
  openGachaAnimation,
  setOpenGachaAnimation,
  gachaType,
}) => {
  const {
    userInfo,
  } = useStateContext();
  const control01 = useAnimation();
  const control02 = useAnimation();

  const [controlNum, setControlNum] = useState<number>(0);
  const [message, setMessage] = useState<string>('');
  const [displayMessage, setDisplayMessage] = useState<string>('');
  const [premierBackground, setPremierBackGround] = useState<string>(`rgba(${convertColorcode(rareDataList[0].color[100])}, 0.8)`);
  const [luxuryBackground, setLuxuryBackground] = useState<string>(`rgba(${convertColorcode(rareDataList[userInfo.wild_rare].color[100])}, 0.8)`);


  // 1
  useEffect(() => {
    const controlText = animate(0, message.length, {
      duration: (message.length / 20),
      onUpdate: (latest: number) => {
        // 
        setDisplayMessage(message.slice(0, Math.round(latest)));
      },
    });
    return controlText.stop;
  }, [message]);

  // 
  useEffect(() => {
    if (gachaType === 'premier') {
      const controlPremierBackground = animate(`rgba(${convertColorcode(rareDataList[0].color[100])}, 0.8)`, [
        `rgba(${convertColorcode(rareDataList[4].color[100])}, 0.8)`, `rgba(${convertColorcode(rareDataList[4].color[200])}, 0.8)`, `rgba(${convertColorcode(rareDataList[4].color[100])}, 0.8)`,
        `rgba(${convertColorcode(rareDataList[2].color[100])}, 0.8)`, `rgba(${convertColorcode(rareDataList[2].color[200])}, 0.8)`, `rgba(${convertColorcode(rareDataList[2].color[100])}, 0.8)`,
        `rgba(${convertColorcode(rareDataList[1].color[100])}, 0.8)`, `rgba(${convertColorcode(rareDataList[1].color[200])}, 0.8)`, `rgba(${convertColorcode(rareDataList[1].color[100])}, 0.8)`,
        `rgba(${convertColorcode(rareDataList[3].color[100])}, 0.8)`, `rgba(${convertColorcode(rareDataList[3].color[200])}, 0.8)`, `rgba(${convertColorcode(rareDataList[3].color[100])}, 0.8)`,
        `rgba(${convertColorcode(rareDataList[4].color[100])}, 0.8)`
      ], {
        duration: 13,
        repeat: Infinity,
        repeatType: 'loop',
        onUpdate: (latest) => {
          setPremierBackGround(latest);
        },
      });
      return controlPremierBackground.stop;

    } else if (gachaType === 'luxury') {
      const controlLuxuryBackground = animate(`rgba(${convertColorcode(rareDataList[userInfo.wild_rare].color[100])}, 0.8)`, [
        `rgba(${convertColorcode(rareDataList[userInfo.wild_rare].color[100])}, 0.8)`,
        `rgba(${convertColorcode(rareDataList[userInfo.wild_rare].color[200])}, 0.8)`,
        `rgba(${convertColorcode(rareDataList[userInfo.wild_rare].color[300])}, 0.8)`,
        `rgba(${convertColorcode(rareDataList[userInfo.wild_rare].color[200])}, 0.8)`,
        `rgba(${convertColorcode(rareDataList[userInfo.wild_rare].color[100])}, 0.8)`,
      ], {
        duration: 5,
        repeat: Infinity,
        repeatType: 'loop',
        onUpdate: (latest) => {
          setLuxuryBackground(latest);
        },
      });
      return controlLuxuryBackground.stop;
    }
    // eslint-disable-next-line
  }, []);

  // 
  useEffect(() => {
    if (controlNum === 0) {
      setControlNum(1);
    } else if (controlNum === 1) {
      setMessage(` `);
      control01.start({
        y: [-1200, -720, 0, -12, -18, -21, -18, -12, 0, -6, -9, -6, 0, -3, 0],
        rotate: [null, 3, 0, -6, 0, 12, 0, -15, 0, 18, 0, -15, 0, 12, 0, -6, 0, 3, 0, 0, 0, 0, 0, 0, 0, 0]
      });
    } else if (controlNum === 2) {
      setMessage(`${userInfo.wild_shiny ? '' : ''} ${userInfo.wild_rare}  `);
      control02.start({ scale: 1, opacity: 1 });
    } else {
      setOpenGachaAnimation(false);
    }
    // eslint-disable-next-line
  }, [controlNum]);


  // 
  const handleAnimationComplete = async () => {
    await sleep(6000);
    setOpenGachaAnimation(false);
  };

  // (https://www.gradientmagic.com/browse)
  const background = (gachaType === 'normal')
    ? `linear-gradient(35deg, rgba(253, 253, 253, 0.03) 0%, rgba(253, 253, 253, 0.03) 53%,rgba(109, 109, 109, 0.03) 53%, rgba(109, 109, 109, 0.03) 59%,rgba(228, 228, 228, 0.03) 59%, rgba(228, 228, 228, 0.03) 66%,rgba(42, 42, 42, 0.03) 66%, rgba(42, 42, 42, 0.03) 95%,rgba(165, 165, 165, 0.03) 95%, rgba(165, 165, 165, 0.03) 100%),linear-gradient(205deg, rgba(62, 62, 62, 0.03) 0%, rgba(62, 62, 62, 0.03) 31%,rgba(200, 200, 200, 0.03) 31%, rgba(200, 200, 200, 0.03) 41%,rgba(30, 30, 30, 0.03) 41%, rgba(30, 30, 30, 0.03) 47%,rgba(151, 151, 151, 0.03) 47%, rgba(151, 151, 151, 0.03) 60%,rgba(95, 95, 95, 0.03) 60%, rgba(95, 95, 95, 0.03) 100%),linear-gradient(30deg, rgba(7, 7, 7, 0.03) 0%, rgba(7, 7, 7, 0.03) 19%,rgba(63, 63, 63, 0.03) 19%, rgba(63, 63, 63, 0.03) 33%,rgba(175, 175, 175, 0.03) 33%, rgba(175, 175, 175, 0.03) 37%,rgba(244, 244, 244, 0.03) 37%, rgba(244, 244, 244, 0.03) 60%,rgba(177, 177, 177, 0.03) 60%, rgba(177, 177, 177, 0.03) 100%)
    , linear-gradient(15deg,
    rgba(${convertColorcode(rareDataList[0].color[100])}, 0.8) 0%,
    rgba(${convertColorcode(rareDataList[1].color[100])}, 0.8) 20%,
    rgba(${convertColorcode(rareDataList[2].color[100])}, 0.8) 50%,
    rgba(${convertColorcode(rareDataList[3].color[100])}, 0.8) 80%,
    rgba(${convertColorcode(rareDataList[4].color[100])}, 0.8) 100%
    )` : (gachaType === 'premier')
      ? `linear-gradient(224deg, rgba(1, 1, 1,0.02) 0%, rgba(1, 1, 1,0.02) 16%,transparent 16%, transparent 76%,rgba(58, 58, 58,0.02) 76%, rgba(58, 58, 58,0.02) 100%),linear-gradient(305deg, rgba(166, 166, 166,0.02) 0%, rgba(166, 166, 166,0.02) 33%,transparent 33%, transparent 79%,rgba(111, 111, 111,0.02) 79%, rgba(111, 111, 111,0.02) 100%),linear-gradient(393deg, rgba(215, 215, 215,0.02) 0%, rgba(215, 215, 215,0.02) 7%,transparent 7%, transparent 90%,rgba(192, 192, 192,0.02) 90%, rgba(192, 192, 192,0.02) 100%),linear-gradient(392deg, rgba(113, 113, 113,0.02) 0%, rgba(113, 113, 113,0.02) 34%,transparent 34%, transparent 73%,rgba(65, 65, 65,0.02) 73%, rgba(65, 65, 65,0.02) 100%),
      radial-gradient(circle at 28% 90%, rgba(113, 113, 113,0.03) 0%, rgba(113, 113, 113,0.03) 24%,transparent 24%, transparent 100%),radial-gradient(circle at 84% 65%, rgba(173, 173, 173,0.03) 0%, rgba(173, 173, 173,0.03) 45%,transparent 45%, transparent 100%),radial-gradient(circle at 46% 12%, rgba(141, 141, 141,0.03) 0%, rgba(141, 141, 141,0.03) 22%,transparent 22%, transparent 100%),radial-gradient(circle at 26% 14%, rgba(119, 119, 119,0.03) 0%, rgba(119, 119, 119,0.03) 35%,transparent 35%, transparent 100%),radial-gradient(circle at 81% 90%, rgba(42, 42, 42,0.03) 0%, rgba(42, 42, 42,0.03) 40%,transparent 40%, transparent 100%),radial-gradient(circle at 28% 100%, rgba(234, 234, 234,0.03) 0%, rgba(234, 234, 234,0.03) 56%,transparent 56%, transparent 100%)
      , linear-gradient(-9deg,
      rgba(${convertColorcode(rareDataList[0].color[100])}, 0.8) 0%,
      ${premierBackground} 35%,
      rgba(${convertColorcode(rareDataList[0].color[500])}, 0.8) 100%
      )`: `linear-gradient(170deg, rgba(2, 2, 2,0.02) 0%, rgba(2, 2, 2,0.02) 51%,transparent 51%, transparent 100%),linear-gradient(212deg, rgba(82, 82, 82,0.01) 0%, rgba(82, 82, 82,0.01) 39%,transparent 39%, transparent 100%),linear-gradient(95deg, rgba(73, 73, 73,0.01) 0%, rgba(73, 73, 73,0.01) 66%,transparent 66%, transparent 100%),linear-gradient(119deg, rgba(174, 174, 174,0.01) 0%, rgba(174, 174, 174,0.01) 67%,transparent 67%, transparent 100%),linear-gradient(43deg, rgba(237, 237, 237,0.03) 0%, rgba(237, 237, 237,0.03) 13%,transparent 13%, transparent 100%),linear-gradient(148deg, rgba(11, 11, 11,0.01) 0%, rgba(11, 11, 11,0.01) 9%,transparent 9%, transparent 100%),linear-gradient(17deg, rgba(233, 233, 233,0.02) 0%, rgba(233, 233, 233,0.02) 13%,transparent 13%, transparent 100%),linear-gradient(198deg, rgba(248, 248, 248,0.02) 0%, rgba(248, 248, 248,0.02) 43%,transparent 43%, transparent 100%),linear-gradient(199deg, rgba(125, 125, 125,0.01) 0%, rgba(125, 125, 125,0.01) 39%,transparent 39%, transparent 100%),linear-gradient(12deg, rgba(2, 2, 2,0.02) 0%, rgba(2, 2, 2,0.02) 46%,transparent 46%, transparent 100%),linear-gradient(341deg, rgba(227, 227, 227,0.02) 0%, rgba(227, 227, 227,0.02) 13%,transparent 13%, transparent 100%),linear-gradient(215deg, rgba(201, 201, 201,0.03) 0%, rgba(201, 201, 201,0.03) 56%,transparent 56%, transparent 100%),linear-gradient(199deg, rgba(189, 189, 189,0.02) 0%, rgba(189, 189, 189,0.02) 2%,transparent 2%, transparent 100%),linear-gradient(34deg, rgba(116, 116, 116,0.03) 0%, rgba(116, 116, 116,0.03) 63%,transparent 63%, transparent 100%),linear-gradient(50deg, rgba(230, 230, 230,0.01) 0%, rgba(230, 230, 230,0.01) 30%,transparent 30%, transparent 100%),linear-gradient(162deg, rgba(10, 10, 10,0.02) 0%, rgba(10, 10, 10,0.02) 58%,transparent 58%, transparent 100%),linear-gradient(327deg, rgba(35, 35, 35,0.01) 0%, rgba(35, 35, 35,0.01) 15%,transparent 15%, transparent 100%),linear-gradient(255deg, rgba(191, 191, 191,0.03) 0%, rgba(191, 191, 191,0.03) 74%,transparent 74%, transparent 100%),linear-gradient(311deg, rgba(210, 210, 210,0.03) 0%, rgba(210, 210, 210,0.03) 46%,transparent 46%, transparent 100%),linear-gradient(272deg, rgba(212, 212, 212,0.01) 0%, rgba(212, 212, 212,0.01) 19%,transparent 19%, transparent 100%)
      , linear-gradient(0deg,
        rgba(${convertColorcode(rareDataList[0].color[800])}, 0.8) 0%,
        ${luxuryBackground} 35%,
        rgba(${convertColorcode(rareDataList[0].color[800])}, 0.8) 90%,
        rgba(${convertColorcode(rareDataList[0].color[900])}, 0.8) 100%
        )`;
  // dragBox
  const constraintsRef = useRef<HTMLDivElement>(null);
  // 
  const action = (
    <IconButton
      color='inherit'
      onClick={() => setControlNum(controlNum + 1)}
    >
      <ArrowDropDownIcon />
    </IconButton>
  );



  return (
    <Dialog
      open={openGachaAnimation}
      onClose={() => setOpenGachaAnimation(false)}
      fullScreen
      sx={{ zIndex: (theme) => theme.zIndex.drawer + 200 }}
    >
      {userInfo.wild_dexNum > 0 &&
        <DialogContent
          sx={{
            background: background,
            overflow: 'hidden'
          }}
        >
          <Container maxWidth='xs' ref={constraintsRef} sx={{ p: 0 }}>
            <IconButton
              onClick={() => setOpenGachaAnimation(false)}
            >
              <CloseIcon sx={{ color: 'white' }} />
            </IconButton>
            <Box
              justifyContent='center'
              alignItems='center'
              sx={{
                mt: '50vh',
                textAlign: 'center',
              }}
            >
              {controlNum === 1 &&
                <motion.img
                  initial={{ y: -1440, rotate: 0 }}
                  animate={control01}
                  drag
                  dragConstraints={constraintsRef}
                  whileHover={{ scale: 1.2 }}
                  whileTap={{ scale: 0.8 }}
                  whileDrag={{ scale: 2.0 }}
                  transition={{
                    y: { ease: 'easeOut', duration: 2, delay: 1 },
                    rotate: { ease: 'easeIn', duration: 2, delay: 3, repeat: Infinity },
                  }}
                  src={rareDataList[userInfo.wild_rare].url}
                  alt={`${userInfo.wild_rare}`}
                  onClick={() => setControlNum(controlNum + 1)}
                >
                </motion.img>
              }

              {controlNum === 2 &&
                <motion.img
                  initial={{ scale: 0, opacity: 0 }}
                  animate={control02}
                  transition={{ type: 'spring', duration: 1, delay: 0 }}
                  src={userInfo.wild_shiny
                    ? `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/shiny/${userInfo.wild_dexNum}.gif`
                    : `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/versions/generation-v/black-white/animated/${userInfo.wild_dexNum}.gif`
                  }
                  alt={`${userInfo.wild_dexNum}`}
                  onAnimationComplete={handleAnimationComplete}
                >
                </motion.img>
              }
            </Box>

            <Snackbar
              open={true}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'center',
              }}
            >
              <SnackbarContent
                message={displayMessage}
                action={action}
                sx={{ bgcolor: 'white', color: 'black' }}
              />
            </Snackbar>
          </Container>
        </DialogContent>
      }
    </Dialog>
  )
}

export default GachaAnimation;
